<template>
  <button class="c-button" :class="styles" disabled>
    <div class="c-button__content">
      <span class="c-button__content">
        <slot />
      </span>
    </div>
  </button>
</template>
<script>
export default {
  name: 'GhostButton',
  props: {
    sprawled: {
      type: Boolean,
      default: false
    },
    box: {
      type: Boolean,
      default: false
    },
    circle: {
      type: Boolean,
      default: false
    },
    fit: {
      type: Boolean,
      default: false
    },
    softly: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    styles() {
      return {
        [this.getClass('sprawled')]: this.sprawled,
        [this.getClass('box')]: this.box,
        [this.getClass('circle')]: this.circle,
        [this.getClass('fit')]: this.fit,
        [this.getClass('softly')]: this.softly
      }
    }
  },
  methods: {
    getClass(name) {
      return `c-button--${name}`
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
