<template>
  <Spacing class="v--flex-gap">
    <Dropdown
      tooltip="Altere o status das respostas"
      title="Escolha um status para ver"
      placement="bottom-end"
      :initial="answerStatusInitial"
      :items="answerStatus"
      @change="$emit('change-status', $event.value)"
    >
      Respostas
    </Dropdown>
    <Dropdown
      v-if="useSentiments"
      tooltip="Altere os sentimentos exibidos"
      title="Escolha os sentimentos que deseja ver"
      placement="bottom-end"
      :initial="sentimentsStatusInitial"
      :disabled="disabledSentiments"
      :items="sentimentsStatus"
      @change="$emit('change-sentiment', $event.value)"
    >
      Sentimento
    </Dropdown>
  </Spacing>
</template>
<script>
export default {
  name: 'AnswerToolbar',
  emits: ['change-sentiment', 'change-status'],
  props: {
    question: {
      type: Number,
      default: 0
    },
    valids: {
      type: Number,
      default: 0
    },
    invalids: {
      type: Number,
      default: 0
    },
    positives: {
      type: Number,
      default: 0
    },
    negatives: {
      type: Number,
      default: 0
    },
    neutrals: {
      type: Number,
      default: 0
    },
    unclassifieds: {
      type: Number,
      default: 0
    },
    sentiments: {
      type: Array,
      default: () => []
    },
    useSentiments: {
      type: Boolean,
      default: true
    },
    disabledSentiments: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    answerStatus() {
      return [
        { name: `Válidas (${this.valids})`, value: 'valids' },
        { name: `Inválidas (${this.invalids})`, value: 'invalids' },
        { name: `Todas (${this.valids + this.invalids})`, value: 'answers' }
      ]
    },
    answerStatusInitial() {
      return this.answerStatus.find((i) => i.value === 'valids')
    },
    sentimentsStatus() {
      return this.sentiments.length
        ? this.sentiments
        : [
            { name: `Positivas (${this.positives})`, value: 'Positive' },
            { name: `Negativas (${this.negatives})`, value: 'Negative' },
            { name: `Neutras (${this.neutrals})`, value: 'Neutral' },
            {
              name: `Sem classificação (${this.unclassifieds})`,
              value: 'Unclassified'
            },
            {
              name: `Todas (${
                this.positives +
                this.negatives +
                this.neutrals +
                this.unclassifieds
              })`,
              value: 'all'
            }
          ]
    },
    sentimentsStatusInitial() {
      const justForUpdates =
        this.positives + this.negatives + this.neutrals + this.unclassifieds
      return (
        justForUpdates >= 0 &&
        this.sentimentsStatus.find((i) => i.value === 'all')
      )
    }
  }
}
</script>
