<template>
  <div>
    <Spacing class="p-file__title-bar" bottom="4">
      <Spacing right="1">
        <GoBackButton :to="'/inicio'" />
      </Spacing>
      <div>
        <h1 class="p-file__title">{{ $route.params.file }}</h1>
        <SurveyNavbar @change-tab="changeTab" />
      </div>
    </Spacing>

    <SurveySubmissions
      v-if="$route.query?.aba === 'respostas'"
      :questions="questions"
      @archive="archive"
      @unarchive="unarchive"
    />
    <SurveyResults
      v-if="$route.query?.aba === 'resultados'"
      :questions="questions"
      @change-class="changeQuestionClass"
      @update-survey-data="updateSurveyData"
      @comment-on-question="showingQuestion = $event"
      @remove-comment="removingComment = $event"
    />
    <CommentModal
      v-if="showingQuestion"
      :loading="$wait.is('commenting-question')"
      @close="showingQuestion = null"
      @comment="comment"
    />
    <CommentDeleteModal
      v-if="removingComment"
      :comment="removingComment.comment"
      @delete="deleteQuestionComment"
      @close="removingComment = null"
    />
    <!--/
    <div v-if="isActive('resultados')" style="position: relative"></div>
    <Form v-if="isActive('pesquisa') && survey.language" />
    <SurveyDeleteModal
      v-if="showDeleteSurvey"
      @close="showDeleteSurvey = null"
    /> -->
  </div>
</template>
<script>
import { mapState } from 'vuex'
// import Form from '@/components/pesquisa/Form/Form.vue'
import CommentModal from '@/components/pesquisa/CommentModal/CommentModal'
import { QuestionClassifier } from '@/plugins/question-classifier'
import survey from '@/mixins/survey'
import comment from '@/mixins/comment'
import SurveyNavbar from '@/components/pesquisa/Navbar/Navbar.vue'
import SurveySubmissions from '@/components/pesquisa/Submissions/Submissions.vue'
import SurveyResults from '@/components/pesquisa/Results/Results.vue'
import CommentDeleteModal from '@/components/pesquisa/Comment/DeleteModal/DeleteModal.vue'
// import SurveyDeleteModal from '@/components/pesquisa/DeleteModal/DeleteModal.vue'
export default {
  name: 'File',
  route: '/pesquisas/:org/:repo/:file',
  layout: 'painel',
  mixins: [survey, comment],
  components: {
    CommentModal,
    CommentDeleteModal,
    // Form,
    SurveyNavbar,
    SurveyResults,
    SurveySubmissions
    // SurveyDeleteModal
  },
  data() {
    return {
      showingQuestion: null,
      showingAnswers: [],
      latex: null,
      showDeleteSurvey: false,
      tab: this.$route.query?.aba || '',
      removingComment: null
    }
  },
  computed: {
    ...mapState({
      settings: (state) => state.auth.settings,
      survey: (state) => state.surveys.survey,
      questionsRaw: (state) => state.questions.questions,
      answers: (state) => state.answers.answers,
      filtereds: (state) => state.answers.filtereds,
      user: (state) => state.auth.user
    }),
    questions() {
      return this.questionsRaw
    },
    githubLink() {
      return `https://github.com/${this.$route.params.org}/${this.$route.params.repo}`
    }
  },
  beforeCreate() {
    this.template = localStorage.getItem('questions-template')

    this.$wait.start('survey')
    this.$wait.start('surveys')

    this.$github
      .getSettingsFile()
      .then(({ data }) => {
        const str = Buffer.from(data.content, 'base64').toString('utf8')
        const settings = {
          sha: data?.sha,
          content: JSON.parse(str)
        }
        this.$store.dispatch('auth/createSettings', settings)
        const exists = settings.content.find(
          (i) => i.repository === this.githubLink,
          this.githubLink
        )
        return this.surveys()
          .get()
          .then(({ survey }) => {
            if (!exists) {
              this.$github.addRepo({
                repository: this.githubLink,
                name: this.$route.params.file,
                survey
              })
            }
            return this.updateSurveyData(survey)
          })
          .catch((error) => {
            if (error?.response?.status === 404) {
              this.removeRepo().then(() => {
                this.$toast.error('A pesquisa solicitada não existe')
                this.$router.push('/inicio')
              })
            } else {
              this.$toast.error('Houve um erro. Por favor, tente novamente.')
            }
          })
          .finally(() => this.$wait.end('survey'))
      })
      .finally(() => {
        this.$wait.end('surveys')
      })
  },
  methods: {
    updateSurveyData(survey) {
      const classifier = new QuestionClassifier()
      const filtereds = survey.filtereds || []
      const questions =
        survey?.classifications?.map((classification, index) => {
          const answers = survey.answers
            .map((answer, line) => ({
              text: answer[index],
              line
            }))
            .map((answer) => {
              const sentiment = this.$answerClassifier.sentiment(
                answer.text,
                this.survey.language
              )

              let text = answer.text

              if (
                this.$questionClassifier.getOption(classification).answers
                  .printStyle === 'OpenQuestion'
              ) {
                const negativeWords = [...new Set(sentiment.negative)].sort(
                  (a, b) => a.length - b.length
                )
                const positiveWords = [...new Set(sentiment.positive)].sort(
                  (a, b) => a.length - b.length
                )

                negativeWords.forEach((word) => {
                  const regex = new RegExp(
                    '(?:[^.\\w]|^|^\\W+)' +
                      word +
                      '(?:[^.\\w]|\\W(?=\\W+|$)|$)',
                    'g'
                  )
                  text = text.replace(
                    regex,
                    `<span class="v--text-tag v--bg-negative">${word}</span>`
                  )
                })

                positiveWords.forEach((word) => {
                  const regex = new RegExp(
                    '(?:[^.\\w]|^|^\\W+)' +
                      word +
                      '(?:[^.\\w]|\\W(?=\\W+|$)|$)',
                    'g'
                  )
                  text = text.replace(
                    regex,
                    `<span class="v--text-tag v--bg-positive">${word}</span>`
                  )
                })
              }

              return {
                ...answer,
                sentiment,
                text,
                originalText: answer.text
              }
            })
          return {
            title: survey.questions[index],
            classification,
            type: classifier.getType(classification),
            column: index,
            answers: answers.filter((i) => !filtereds.includes(i.line)),
            noanswers: answers.filter((i) => filtereds.includes(i.line))
          }
        }) || []
      const answers = survey.answers.filter(
        (i, index) => !filtereds.includes(index)
      )
      const noanswers = survey.answers.filter((i, index) =>
        filtereds.includes(index)
      )
      this.currentAnswerIndex = 0
      this.$store.commit('answers/setFiltereds', filtereds)
      this.$store.commit('answers/setAnswers', answers)
      this.$store.commit('answers/setNoAnswers', noanswers)
      this.$store.commit('questions/setQuestions', questions)
      return this.$store.commit('surveys/setSurvey', survey)
    },
    isArchived(line) {
      return this.filtereds.includes(line)
    },

    isActive(tab) {
      this.$route.query.aba === tab
    },
    changeTab(aba) {
      this.tab = aba
      const query = {
        ...this.$route.query,
        aba
      }

      if (aba === 'pesquisa') {
        query.etapa = 'fundamentacao'
      } else {
        delete query.etapa
      }

      this.$router.replace({
        ...this.$route,
        query
      })
    },
    goToQuestion(index) {
      var element = this.$refs[`question-${index}`][0].$el
      element.scrollIntoView()
    },
    comment(message) {
      this.$wait.start('commenting-question')
      const comment = {
        message,
        author: `${this.user.login}@${this.user.id}`
      }

      this.comments()
        .onQuestion(comment, this.showingQuestion.column)
        .then(() => {
          this.$toast.success('Comentário registrado!')
        })
        .finally(() => {
          this.showingQuestion = null
          this.$wait.end('commenting-question')
        })
    },
    changeQuestionClass($event) {
      this.$wait.start(`changing-question-${$event.question}`)
      const classifications = [...this.survey.classifications]
      classifications[$event.question] = $event.class
      this.surveys()
        .update({ classifications }, 'Update classifications')
        .then((survey) => {
          this.updateSurveyData(survey)
          this.$toast.success(`Classificação modificada`)
        })
        .finally(() => {
          this.$wait.end(`changing-question-${$event.question}`)
        })
    },
    archive(response) {
      this.$wait.start('survey')
      const filtereds = [...(this.filtereds || []), response]
      this.surveys()
        .update({ filtereds }, 'Add filtered')
        .then((survey) => {
          this.updateSurveyData(survey)
          this.$toast.success(`Resposta arquivada`)
        })
        .finally(() => {
          this.$wait.end('survey')
        })
    },
    unarchive(response) {
      this.$wait.start('survey')
      const filtereds = [...(this.filtereds || [])].filter(
        (i) => i !== response
      )
      this.surveys()
        .update({ filtereds }, 'Remove filtered')
        .then((survey) => {
          this.updateSurveyData(survey)
          this.$toast.success(`Resposta desarquivada`)
        })
        .finally(() => {
          this.$wait.end('survey')
        })
    },
    removeRepo() {
      return this.$github
        .removeRepo(this.githubLink, this.$route.params.file)
        .then((settings) => {
          return this.$store.dispatch('auth/createSettings', settings)
        })
    },
    deleteSurvey() {
      this.$wait.start('removing-repo')
      this.$github
        .deleteFolder(this.githubLink, `database/${this.$route.params.file}`)
        .then(() => {
          return this.removeRepo().then(() => {
            this.$toast.success('Removido!')
            this.$router.push('/inicio')
          })
        })
        .finally(() => this.$wait.end('removing-repo'))
    },
    deleteQuestionComment() {
      this.$wait.start('removing-comment')
      this.comments()
        .deleteFromQuestion(
          this.removingComment.comment,
          this.removingComment.question.column
        )
        .then(() => this.$toast.success(`Comentário removido`))
        .finally(() => {
          this.removingComment = null
          this.$wait.end('removing-comment')
        })
    }
  }
}
</script>
<style lang="stylus" scoped>
.p-file
  &__title
    font-weight 500
    font-size 20px
  &__title-bar
    display flex
    align-items flex-start
  &__page-menu
    display flex

  &__container
    display grid
    grid-gap 24px
    grid-template-columns 250px 1fr
    height calc(100vh - 240px)
    // overflow-y hidden
  &__body
    height 100%
  &__questions
    height calc(100vh - 306px)
    // overflow auto
    display grid
    grid-gap 8px
  &__menu
    display grid
    grid-gap 4px
    grid-template-columns 1fr
    grid-auto-rows min-content
    // overflow auto
    padding 10px
    background-color rgba(247, 249, 249, .4)
    border 1px solid rgba(247, 249, 249, 1)
    height min-content
    border-radius 8px
</style>
