<template>
  <div ref="affixed" class="c-affixed" :style="styles">
    <slot />
  </div>
</template>
<script>
export default {
  name: 'Affixed',
  props: {
    offset: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      offsetTop: 0,
      top: 0
    }
  },
  computed: {
    styles() {
      let top = this.top - this.offsetTop
      if (top < 0) {
        top = 0
      }
      return {
        transform: `translateY(${top}px)`
      }
    }
  },
  mounted() {
    const child = this.$slots.default()
    if (child) {
      const { top } = child[0].elm.getBoundingClientRect()
      const { scrollTop } = document.documentElement
      this.offsetTop = top - this.offset + scrollTop
    }

    window.addEventListener('scroll', this.onScroll)
    window.addEventListener('resize', this.onResize)
  },
  unmounted() {
    window.removeEventListener('resize', this.onResize)
    window.removeEventListener('scroll', this.onScroll)
  },
  methods: {
    onScroll() {
      if (this.$refs && this.$refs.affixed) {
        const { scrollTop } = document.documentElement
        if (scrollTop >= this.offsetTop) {
          this.top = scrollTop
        } else {
          this.top = 0
        }
        // if (this.offsetTop <= window.scrollY) {
        //   this.left = this.offsetLeft + this.offset + 'px'
        //   this.top = 0 + this.offset + 'px'
        //   this.fixed = true
        //   this.$emit('fixed')
        // } else {
        //   this.$emit('no-fixed')
        //   this.left = `auto`
        //   this.top = 0 + this.offset + 'px'
        //   this.fixed = false
        // }
      }
    }
  }
}
</script>
<style lang="stylus" src="./styles.styl" scoped></style>
