<template>
  <Modal>
    <FieldSelect
      name="chart"
      :selected="chart"
      :options="[
        { name: 'Gráfico de Barras', value: 'Bars' },
        { name: 'Gráfico de Pizza', value: 'Pie' },
        { name: 'Gráfico de Linha', value: 'Line' }
      ]"
      v-model="chart"
    />
    <Bar v-if="chart === 'Bars'" :question="question" @download="download" />
    <Pie v-if="chart === 'Pie'" :question="question" @download="download" />
    <Line v-if="chart === 'Line'" :question="question" @download="download" />
  </Modal>
</template>
<script>
import Bar from '@/components/pesquisa/Question/Charts/Bar.vue'
import Pie from '@/components/pesquisa/Question/Charts/Pie.vue'
import Line from '@/components/pesquisa/Question/Charts/Line.vue'

export default {
  name: 'Charts',
  components: {
    Bar,
    Pie,
    Line
  },
  props: {
    question: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      chart: 'Bars'
    }
  },
  methods: {
    download(key) {
      var link = document.createElement('a')
      link.download = `${key}.png`
      link.href = document.getElementById(key).toDataURL()
      link.click()
    }
  }
}
</script>
