<template>
  <div class="c-open-question">
    <Toolbar
      :question="question"
      :valids="valids.length"
      :invalids="invalids.length"
      :answers="answers.length"
      :positives="groups['Positive']?.length"
      :negatives="groups['Negative']?.length"
      :neutrals="groups['Neutral']?.length"
      :disabled-sentiments="resource === 'chart'"
      @change-status="setKind($event)"
      @change-sentiment="setGroup($event)"
    />
    <Spacing vertical="1" class="v--flex-gap">
      <Button
        with-icon
        @click="setResource('list')"
        :selected="resource === 'list'"
      >
        <Icon name="list" /> <span>Lista</span>
      </Button>
      <Button
        with-icon
        @click="setResource('chart')"
        :selected="resource === 'chart'"
      >
        <Icon name="bar-chart" /> <span>Gráfico de classificações</span>
      </Button>
      <Button
        with-icon
        :selected="resource === 'word-cloud'"
        @click="setResource('word-cloud')"
      >
        <Icon name="word-cloud" size="14" />
        <span>Nuvem de palavras </span>
      </Button>
    </Spacing>
    <Spacing>
      <div style="position: relative">
        <div
          v-if="
            resource === 'word-cloud' && percentage > 0 && percentage !== 100
          "
          style="
            height: 300px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            width: 100%;
          "
        >
          <div style="width: 250px; text-align: center">
            <span style="font-size: 20px; font-weight: 700">
              {{ percentage.toFixed(2) }}%
            </span>
            <ProgressBar bar-only :percentage="percentage.toFixed(2)" />
          </div>
        </div>
      </div>
    </Spacing>
    <div v-if="resource === 'word-cloud'" class="c-open-question__answers">
      <VueWordCloud
        style="height: 300px; width: 100%"
        :words="words"
        :color="
          ([, weight]) =>
            weight > 10 ? 'DeepPink' : weight > 5 ? 'RoyalBlue' : 'Indigo'
        "
        :enter-animation="{
          opacity: 0,
          transform: 'scale3d(0.3,0.3,0.3)'
        }"
        font-family="Roboto"
        @percentage="percentage = $event"
      />
    </div>
    <div v-if="resource === 'chart'" class="c-open-question__answers">
      <HorizontalChart
        :set="set"
        :total="itemsLength"
        @show-answers="showAnswers"
      />
    </div>
    <div v-if="resource === 'list'" class="c-open-question__answers">
      <OpenQuestionAnswer
        v-for="(answer, index) in items"
        :key="index"
        :answer="answer"
        :question="question"
        :encrypt="encrypt"
        :hide-interactive-items="false"
        @open-answer-modal="answerShowing = answer"
        @delete-comment="showRemoveModal = $event"
      />
    </div>
    <CommentModal
      v-if="answerShowing"
      :loading="$wait.is('commenting-answer')"
      :answer="answerShowing"
      :encrypt="encrypt"
      @close="answerShowing = null"
      @comment="comment"
    >
      <OpenQuestionAnswer
        :hide-interactive-items="true"
        :question="question"
        :answer="answerShowing"
      />
    </CommentModal>
    <Modal
      v-if="showRemoveModal"
      title="Excluir o comentário?"
      small
      @close="showRemoveModal = false"
    >
      <Comment :comment="showRemoveModal" hide-delete-button />
      <template v-slot:footer>
        <div class="v--flex-gap v--flex-align-right">
          <Button
            @click="showRemoveModal = false"
            :disabled="$wait.is('removing-comment')"
          >
            Cancelar
          </Button>
          <Button
            class="v--bg-amaranth"
            @click="deleteAnswerComment(showRemoveModal)"
            :loading="$wait.is('removing-comment')"
          >
            Excluir comentário
          </Button>
        </div>
      </template>
    </Modal>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import OpenQuestionAnswer from './Answer'
import Toolbar from '@/components/pesquisa/answers/Toolbar/Toolbar.vue'
import CommentModal from '@/components/pesquisa/CommentModal/CommentModal.vue'
import Comment from '@/components/pesquisa/Comment/Comment.vue'
import github from '@/mixins/survey'
import comment from '@/mixins/commenter'
export default {
  name: 'OpenQuestion',
  emits: ['show-answers'],
  components: {
    Toolbar,
    CommentModal,
    Comment,
    OpenQuestionAnswer
  },
  mixins: [github, comment],
  props: {
    answers: {
      type: Array,
      default: () => []
    },
    open: {
      type: Boolean,
      default: true
    },
    encrypt: {
      type: Boolean,
      default: false
    },
    question: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      percentage: 0,
      answerShowing: null,
      kind: 'valids',
      lastResource: 'list',
      group: 'all',
      resource: 'list',
      layers: false
    }
  },
  created() {
    if (!this.open) {
      this.setResource('')
    }
  },
  mounted() {
    console.log()
  },
  watch: {
    open(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.setResource(newValue ? this.lastResource : '')
      }
    }
  },
  computed: {
    ...mapState({
      user: (state) => state.auth.user,
      survey: (state) => state.surveys.survey
    }),
    words() {
      return this.$word.count(
        this.items.flatMap((item) => item.originalText).join(' '),
        this.survey.language
      )
    },
    groups() {
      return this.$answerClassifier.groupBySentiment(this[this.kind])
    },
    set() {
      return Object.keys(this.groups).reduce((acc, item) => {
        acc[item] = this.groups[item].length
        return acc
      }, {})
    },
    grouped() {
      return this.groups[this.group]
    },
    valids() {
      return this.answers.filter((i) => this.$answerClassifier.isValid(i.text))
    },
    invalids() {
      return this.answers.filter((i) => !this.$answerClassifier.isValid(i.text))
    },
    itemsLength() {
      return this[this.kind].length
    },
    items() {
      const kind = this.group === 'all' ? this.kind : 'grouped'

      return this[kind]
    },
    latexItems() {
      return { total: this.itemsLength, set: this.set }
    }
  },
  methods: {
    showAnswers(label) {
      return this.$emit(
        'show-answers',
        this.groups[label].map((item) => item.line)
      )
    },
    setGroup(value) {
      this.group = value
    },
    setResource(value) {
      this.lastResource = this.resource || 'list'
      this.resource = this.resource === value ? '' : value
    },
    setKind(value) {
      this.kind = value
    },
    comment(message) {
      this.$wait.start('commenting-answer')
      const comment = {
        message,
        author: `${this.user.login}@${this.user.id}`
      }

      const call = this.encrypt
        ? this.createCodingComment(
            this.user,
            comment,
            this.question,
            this.answerShowing.line
          )
        : this.commentOnAnswer(comment, this.question, this.answerShowing.line)
      call
        .then(() => {
          this.$toast.success('Comentário registrado!')
        })
        .finally(() => {
          this.answerShowing = null
          this.$wait.end('commenting-answer')
        })
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
