<template>
  <div class="c-horizontal-chart">
    <template v-if="items.length">
      <ProgressBar
        v-for="item in items"
        :key="item"
        :label="item"
        :total="total"
        :value="set[item]"
        :disable-interactions="disableInteractions"
        @show-answers="$emit('show-answers', item)"
      />
    </template>
    <Spacing v-else vertical="2">Não há dados para exibir.</Spacing>
  </div>
</template>
<script>
export default {
  name: 'HorizontalChart',
  props: {
    total: {
      type: Number,
      default: 0
    },
    set: {
      type: Object,
      default: () => ({})
    },
    disableInteractions: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    items() {
      return Object.keys(this.set).sort((a, b) => {
        return this.set[b] - this.set[a]
      })
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
