<template>
  <div
    class="c-open-question__answer"
    :class="`c-open-question__answer--${sentiment(answer).toLowerCase()}`"
  >
    <p
      :class="{ 'c-open-question--hide-layers': !layers }"
      class="c-open-question__answer-text"
      v-html="answer.text"
    />
    <div v-if="!hideInteractiveItems" class="c-open-question__controls">
      <Button
        circle
        class="v--bg-white v--text-color-bright-blue"
        @click="toggleAnswers"
      >
        <Icon name="message-circle" size="14" />
      </Button>
      <Button
        circle
        :selected="layers"
        @click="toggleLayers"
        class="v--bg-white v--text-color-bright-blue"
      >
        <Icon name="layers" size="14" />
      </Button>
      <Button
        circle
        :selected="cloud"
        @click="toggleCloud"
        class="v--bg-white v--text-color-bright-blue"
      >
        <Icon name="word-cloud" size="14" />
      </Button>
      <Dropdown
        hide-indicator
        circle
        :change-name-on-select="false"
        :button-classes="['v--bg-white', 'v--text-color-bright-blue']"
        :items="sentimentOptions"
      >
        <Icon name="repeat" size="14" />
      </Dropdown>
    </div>
    <template v-if="!hideInteractiveItems">
      <CommentsContainer v-if="answers">
        <Comment
          v-for="(comment, index) in comments"
          :comment="comment"
          :key="index"
          @delete="
            $emit('delete-comment', {
              ...$event,
              answer: answer.line,
              question: question
            })
          "
        />
      </CommentsContainer>
      <Spacing v-if="layers" top="2" bottom="1">
        <ul>
          <li>xcore: {{ answer.sentiment.score }}</li>
          <li>Comparative: {{ answer.sentiment.comparative }}</li>
        </ul>
      </Spacing>
      <div style="position: relative">
        <div
          v-if="cloud && percentage > 0 && percentage !== 100"
          style="
            height: 300px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            width: 100%;
          "
        >
          <div style="width: 250px; text-align: center">
            <span style="font-size: 20px; font-weight: 700">
              {{ percentage.toFixed(2) }}%
            </span>
            <ProgressBar bar-only :percentage="percentage.toFixed(2)" />
          </div>
        </div>
      </div>
      <Spacing v-if="cloud" top="2" bottom="1">
        <VueWordCloud
          style="height: 300px; width: 100%"
          :words="words"
          :color="
            ([, weight]) =>
              weight > 10 ? 'DeepPink' : weight > 5 ? 'RoyalBlue' : 'Indigo'
          "
          :enter-animation="{
            opacity: 0,
            transform: 'scale3d(0.3,0.3,0.3)'
          }"
          font-family="Roboto"
          @percentage="percentage = $event"
        />
      </Spacing>
    </template>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Comment from '@/components/pesquisa/Comment/Comment.vue'
import CommentsContainer from '@/components/pesquisa/CommentsContainer/CommentsContainer.vue'
export default {
  name: 'OpenQuestionAnswer',
  emits: ['open-answer-modal'],
  components: {
    Comment,
    CommentsContainer
  },
  props: {
    answer: {
      type: Object,
      default: () => ({})
    },
    question: {
      type: Number,
      required: true
    },
    encrypt: {
      type: Boolean,
      default: false
    },
    hideInteractiveItems: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      percentage: 0,
      layers: false,
      cloud: false,
      answers: true
    }
  },
  computed: {
    ...mapState({
      survey: (state) => state.surveys.survey,
      coding: (state) => state.codes.codes
    }),
    comments() {
      if (
        this.encrypt &&
        this.coding?.comments?.answers?.[this.question]?.[this.answer.line]
      ) {
        return (
          [
            ...(this.survey?.comments?.answers?.[this.question]?.[
              this.answer.line
            ] || []),
            ...(this.encrypt &&
              (this.coding?.comments?.answers?.[this.question]?.[
                this.answer.line
              ] ||
                []))
          ].sort((a, b) => b.created_at - a.created_at) || []
        )
      }
      return (
        this.survey?.comments?.answers?.[this.question]?.[this.answer.line] ||
        []
      )
    },
    words() {
      return this.$word.count(this.answer.originalText, this.survey.language)
    },
    sentimentOptions() {
      const items = [
        { name: 'Tornar positivo', value: 'Positive' },
        { name: 'Tornar negativo', value: 'Negative' },
        { name: 'Tornar neutro', value: 'Neutral' }
      ]
      return items.filter((i) => i.value !== this.sentiment(this.answer))
    }
  },
  methods: {
    sentiment(answer) {
      return answer?.sentiment?.score > 1
        ? 'Positive'
        : answer?.sentiment?.score < -1
        ? 'Negative'
        : 'Neutral'
    },
    toggleCloud() {
      this.cloud = !this.cloud
      if (this.layers) {
        this.layers = false
      }
      if (this.answers) {
        this.answers = false
      }
    },
    toggleLayers() {
      this.layers = !this.layers
      if (this.cloud) {
        this.cloud = false
        this.percentage = 0
      }
      if (this.answers) {
        this.answers = false
      }
    },
    toggleAnswers() {
      this.$emit('open-answer-modal')
      if (!this.answers) {
        this.answers = true
      }
      if (this.cloud) {
        this.cloud = false
        this.percentage = 0
      }
      if (this.layers) {
        this.layers = false
      }
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
