<template>
  <div
    class="c-card"
    :class="{
      'v--hoverable': false,
      'c-card--horizontal': horizontal
    }"
  >
    <div class="c-card__content">
      <slot name="more" />
      <router-link class="c-card__header" :to="route">
        <h2 class="c-card__title">
          {{ pesquisa.name }}
        </h2>
        <p class="c-card__subtitle">{{ repo }} - {{ language }}</p>
      </router-link>
      <div class="v--flex-gap" style="gap: 8px">
        <span class="v--flex-gap v--flex-align-middle">
          <Icon name="users" size="14" /> <span>{{ pesquisa.answers }}</span>
        </span>
        <span class="v--flex-gap v--flex-align-middle">
          <Icon name="bar-chart" size="14" />
          <span>{{ pesquisa.questions }}</span>
        </span>
        <span class="c-card__last-update">
          Atualizado em:
          {{ pesquisa.updated_at || '12/08/2021 08:23:01' }}
        </span>
      </div>
    </div>
    <Dropdown
      class="c-card__options"
      hide-indicator
      circle
      :change-name-on-select="false"
      :button-classes="['v--bg-white', 'v--text-color-bright-blue']"
      :items="[
        { name: 'Copiar link', value: 'copy' },
        { name: 'Excluir', value: 'delete' }
      ]"
      @change="doAction"
    >
      <Icon name="more-horizontal" size="14" />
    </Dropdown>
  </div>
</template>
<script>
export default {
  name: 'PesquisasCard',
  props: {
    pesquisa: {
      type: Object,
      default: () => ({})
    },
    horizontal: {
      type: Boolean,
      default: false
    },
    draggable: {
      type: String,
      default: 'false'
    },
    hoverable: {
      type: Boolean,
      default: true
    }
  },
  emits: ['delete-survey'],
  computed: {
    repo() {
      return this.$github.repository(this.pesquisa.repository)
    },
    language() {
      return this.pesquisa.language === 'ptBr' ? 'Português' : 'Inglês'
    },
    shareLink() {
      return window.location.host + this.route + '&shareLink'
    },
    route() {
      return `/pesquisas/${this.repo}/${this.pesquisa.name}?aba=resultados`
    }
  },
  methods: {
    copyLink() {
      this.$copyText(this.shareLink)
        .then(() => this.$toast.success('Link copiado!'))
        .catch(() => this.$toast.error('Falha ao copiar'))
    },
    doAction(item) {
      if (item.value === 'copy') {
        this.copyLink()
      }
      if (item.value === 'delete') {
        this.$emit('delete-survey', this.pesquisa)
      }
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
