<template>
  <div class="c-paginator">
    <Field
      v-model="page"
      :max="paginator.total"
      name="page"
      style="width: 80px"
      type="number"
      @input="loadPage"
    />
    <span class="c-paginator__text">de {{ paginator.total }}</span>
    <Button size="TINY" :disabled="!canGoBack" @click="goBack">
      <Icon name="chevron-left" :size="24" />
    </Button>
    <Button size="TINY" :disabled="!canGoAhead" @click="goAhead">
      <Icon name="chevron-right" :size="24" />
    </Button>
  </div>
</template>
<script>
export default {
  name: 'Paginator',
  emits: ['load-page'],
  props: {
    paginator: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      page: this.paginator.current
    }
  },
  computed: {
    canGoAhead() {
      return this.paginator.current < this.paginator.total
    },
    canGoBack() {
      return this.paginator.current > 1
    }
  },
  methods: {
    loadPage() {
      if (this.page > this.paginator.total) {
        this.page = 1
      }
      this.$emit('load-page', this.page)
    },
    goAhead() {
      this.page = this.paginator.current + 1
      this.loadPage()
    },
    goBack() {
      this.page = this.paginator.current - 1
      this.loadPage()
    }
  }
}
</script>

<style src="./style.styl" lang="stylus" scoped />
