<template>
  <div class="l-painel">
    <header class="l-painel__header">
      <Navbar />
    </header>
    <div class="l-painel__content">
      <slot></slot>
    </div>
  </div>
</template>
<script>
import Navbar from '@/components/layouts/Navbar/Navbar'

export default {
  name: 'Painel',
  components: {
    Navbar
  },
  created() {
    this.$wait.start('getting-settings')

    return this.$github
      .createSettings()
      .then(({ data }) => {
        if (data?.content?.sha) {
          const settings = {
            sha: data?.content?.sha,
            content: JSON.parse('[]')
          }
          this.$storage.setItem('settings', settings)
          this.$store.dispatch('auth/createSettings', settings)
        } else {
          const str = Buffer.from(data.content, 'base64').toString('utf8')
          const settings = {
            sha: data?.sha,
            content: JSON.parse(str)
          }
          this.$store.dispatch('auth/createSettings', settings)
        }
      })
      .catch(() => {
        this.$toast.error('Não conseguimos recuperar as configurações.')
        this.$store.dispatch('auth/logout').then(() => {
          this.$storage.clear()
          this.$router.push('/')
        })
      })
      .finally(() => {
        this.$wait.end('getting-settings')
      })
  }
}
</script>
