<template>
  <Button center @click.stop="goBack" class="v--bg-white" circle>
    <Icon name="arrow-left" :size="20" />
  </Button>
</template>

<script>
export default {
  name: 'GoBackButton',
  props: {
    to: {
      type: [String, Object],
      default: ''
    },
    forcePush: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    goBack() {
      return this.$router.push(this.to)
    }
  }
}
</script>
