<template>
  <div>
    <Spacing vertical="1">
      <Toolbar
        :valids="valids.length"
        :invalids="invalids.length"
        :answers="answers.length"
        :positives="groups['Positive']?.length"
        :negatives="groups['Negative']?.length"
        :neutrals="groups['Neutral']?.length"
        :unclassifieds="groups['Unclassified']?.length"
        @change-status="setKind($event)"
        @change-sentiment="setGroup($event)"
      />
    </Spacing>
    <HorizontalChart
      v-if="resource === 'chart'"
      :set="set"
      :total="itemsLength"
      @show-answers="showAnswers"
    />
    <LatexChart v-if="resource === 'latex'" :total="itemsLength" :set="set" />
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Toolbar from '@/components/pesquisa/answers/Toolbar/Toolbar.vue'

export default {
  name: 'YesOrNot',
  emits: ['show-answers'],
  components: {
    Toolbar
  },
  props: {
    answers: {
      type: Array,
      default: () => []
    },
    open: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      kind: 'valids',
      group: 'all',
      lastResource: 'chart',
      resource: 'chart'
    }
  },
  created() {
    if (!this.open) {
      this.setResource('')
    }
  },
  watch: {
    open(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.setResource(newValue ? this.lastResource : '')
      }
    }
  },
  computed: {
    ...mapState({
      survey: (state) => state.surveys.survey
    }),
    groups() {
      return this.$answerClassifier.groupByCategories(
        'yesOrNot',
        this[this.kind],
        this.survey.language
      )
    },
    grouped() {
      return this.groups[this.group]
    },
    valids() {
      return this.answers.filter((i) => this.$answerClassifier.isValid(i.text))
    },
    invalids() {
      return this.answers.filter((i) => !this.$answerClassifier.isValid(i.text))
    },
    itemsLength() {
      const kind = this.group === 'all' ? this.kind : 'grouped'

      return this[kind].length
    },
    set() {
      const kind = this.group === 'all' ? this.kind : 'grouped'

      return this[kind].reduce((set, item) => {
        const key = item.text === '' ? '' : item.text
        set = {
          ...set,
          [key]: set[key] ? set[key] + 1 : 1
        }

        return set
      }, {})
    }
  },
  methods: {
    setGroup(value) {
      this.group = value
    },
    setKind(value) {
      this.kind = value
    },
    setResource(value) {
      this.lastResource = this.resource || 'chart'
      this.resource = this.resource === value ? '' : value
    },
    showAnswers(label) {
      return this.$emit(
        'show-answers',
        this.answers
          .filter((item) => item.text === label)
          .map((item) => item.line)
      )
    }
  }
}
</script>
