<template>
  <div class="c-comment">
    <img
      class="c-comment__image"
      :src="`https://avatars.githubusercontent.com/u/${author.id}?v=4`"
      :title="author.username"
      :alt="author.username"
    />
    <div class="c-comment__body">
      <div class="c-comment__header">
        <span class="c-comment__header-title">
          <b>@{{ author.username }}</b>
          <span class="c-comment__date">{{ date }}</span>
        </span>
        <Button
          v-if="comment.encrypt"
          circle
          fit
          style="padding: 4px !important"
          v-tooltip="'Só você pode ver'"
        >
          <Icon name="lock" size="11" />
        </Button>
      </div>
      <div class="c-comment__message">
        {{ comment.message }}
      </div>
      <Button
        v-if="parseInt(user?.id) === parseInt(author.id) && !hideDeleteButton"
        link-style
        @click="$emit('delete', comment)"
      >
        Excluir
      </Button>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { formatDistance } from 'date-fns'
import ptBR from 'date-fns/locale/pt-BR'

export default {
  name: 'Comment',
  props: {
    comment: {
      type: Object,
      default: () => ({})
    },
    hideDeleteButton: {
      type: Boolean,
      default: false
    },
    hideCommentButton: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState({
      user: (state) => state.auth.user
    }),
    author() {
      const parts = this.comment.author.split('@')
      return {
        username: parts[0],
        id: parts[1]
      }
    },
    date() {
      return formatDistance(new Date(this.comment.created_at), new Date(), {
        addSuffix: true,
        locale: ptBR
      })
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
