const isQuotaExceeded = (e) => {
  let quotaExceeded = false
  if (e) {
    if (e.code) {
      switch (e.code) {
        case 22:
          quotaExceeded = true
          break
        case 1014:
          // Firefox
          if (e.name === 'NS_ERROR_DOM_QUOTA_REACHED') {
            quotaExceeded = true
          }
          break
      }
    } else if (e.number === -2147024882) {
      // Internet Explorer 8
      quotaExceeded = true
    }
  }
  return quotaExceeded
}

export class Storage {
  type
  ctx
  timeout
  timeoutId
  constructor(ctx, type = 'localStorage') {
    this.type = type
    this.ctx = ctx
    this.timeout = 3000
    this.timeoutId = null
  }

  handleError(e) {
    if (isQuotaExceeded(e)) {
      this.ctx.$toast.error('Persistent storage maximum size reached.')
    }
    if (this.ctx) {
      if (this.timeoutId === null) {
        if (this.ctx.$toast) {
          this.ctx.$toast.error(
            'Os cookies do seu navegador estão desativados.'
          )
          this.timeoutId = window.setTimeout(() => {
            this.timeoutId = null
          }, this.timeout)
        }
      }
    }
  }

  handleMethod(method, payload) {
    try {
      const { key, value } = payload

      switch (method) {
        case 'setItem': {
          window[this.type].setItem(key, JSON.stringify(value))
          break
        }
        case 'getItem': {
          const string = window[this.type].getItem(key)
          return JSON.parse(string || '')
        }
        case 'removeItem':
          return window[this.type].removeItem(key)
        case 'hasItem':
          return !!window[this.type].getItem(key)
        default:
          return null
      }
    } catch (e) {
      this.handleError(e)
    }
  }

  setItem(key, value) {
    return this.handleMethod('setItem', { key, value })
  }

  getItem(key) {
    return this.handleMethod('getItem', { key })
  }

  clear() {
    return window[this.type].clear()
  }

  removeItem(key) {
    return this.handleMethod('removeItem', { key })
  }

  hasItem(key) {
    return this.handleMethod('hasItem', { key })
  }
}

export default {
  install: (app) => {
    const storage = new Storage(app)
    app.config.globalProperties.$storage = storage
  }
}
