export function getTextColor(hexadecimal) {
  /*
  From this W3C document: http://www.w3.org/TR/AERT#color-contrast

  Color brightness is determined by the following formula:
  ((Red value X 299) + (Green value X 587) + (Blue value X 114)) / 1000
  */

  const threshold = 200 /* Lower threshold equals more dark text on dark background  */

  function cutHex(h) {
    return h.charAt(0) === '#' ? h.substring(1, 7) : h
  }

  const red = parseInt(cutHex(hexadecimal).substring(0, 2), 16)
  const green = parseInt(cutHex(hexadecimal).substring(2, 4), 16)
  const blue = parseInt(cutHex(hexadecimal).substring(4, 6), 16)

  const cBrightness = (red * 299 + green * 587 + blue * 114) / 1000

  return cBrightness > threshold ? '#484848' : '#ffffff'
}

export default {
  methods: {
    getTextColor(color) {
      return getTextColor(color)
    }
  }
}
