<template>
  <div class="c-toggle-field__wrapper">
    <div
      class="c-toggle-field"
      :class="[
        dark && 'c-toggle-field--dark',
        hasError && 'c-toggle-field--has-error'
      ]"
      @click.stop="$emit('click', $event)"
    >
      <input
        :id="$vnode?.key || radioId || name"
        class="c-toggle-field_trusted-box"
        :type="type"
        :name="name"
        :disabled="disabled"
        :value="value"
        :checked="checked"
        @change="onChange"
      />
      <label
        class="c-toggle-field_label"
        :class="{ 'v--disabled': disabled }"
        :for="$vnode?.key || radioId || name"
      >
        <div
          v-tooltip="tooltip"
          class="c-toggle-field_false-box"
          :class="{
            'v--radio': type === 'radio',
            'v--disabled': disabled,
            'v--semi-checked': semiChecked
          }"
        >
          <Icon name="check" class="c-toggle-field_icon" size="14" />
        </div>
        <div>
          <span>
            <slot>
              {{ label }}
            </slot>
          </span>
          <div class="c-toggle-field__sublabel">{{ sublabel }}</div>
        </div>
      </label>
    </div>
    <div v-if="hasError" class="c-toggle-field_message">
      {{ errorMessage }}
    </div>
  </div>
</template>
<script>
export default {
  name: 'ToggleField',
  props: {
    label: {
      type: String,
      default: ''
    },
    sublabel: {
      type: String,
      default: ''
    },
    value: {
      type: [Number, String],
      default: ''
    },
    radioId: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'checkbox'
    },
    hasError: {
      type: Boolean,
      default: false
    },
    errorMessage: {
      type: String,
      default: 'Faltou preencher aqui.'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    semiChecked: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    },
    tooltip: {
      type: String,
      default: ''
    }
  },
  emits: ['click', 'change', 'input', 'disabled'],
  methods: {
    onChange(e) {
      if (!this.disabled) {
        this.$emit('change', {
          checked: e.target.checked,
          value: this.value
        })
        this.$emit('input', this.value)
      } else {
        this.$emit('disabled', e)
      }
    }
  }
}
</script>

<style lang="stylus" src="./style.styl" scoped />
