<template>
  <Modal @close="$emit('close')" small>
    <slot />
    <div style="height: 40px" class="v--flex v--flex-align-middle">
      <Line vertical />
      <Spacing left="1">
        <small> Escrevendo como {{ user.name }}</small></Spacing
      >
    </div>
    <div class="v--flex">
      <img
        class="c-comment-modal__avaliator"
        :src="user.avatar_url"
        :alt="user.name || 'avatar'"
      />
      <FieldText
        name="comment"
        autofocus
        silent
        rows="5"
        placeholder="Escreva suas considerações"
        :disabled="loading"
        v-model="comment"
      />
    </div>
    <template v-slot:footer>
      <Button
        class="v--bg-star-command-blue v--flex-align-right"
        :loading="loading"
        @click="doComment"
      >
        Comentar
      </Button>
    </template>
  </Modal>
</template>
<script>
import { mapState } from 'vuex'

export default {
  name: 'CommentModal',
  emits: ['comment'],
  props: {
    answer: {
      type: Object,
      default: () => ({})
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      comment: ''
    }
  },
  computed: {
    ...mapState({
      user: (state) => state.auth.user
    })
  },
  methods: {
    doComment() {
      this.$emit('comment', this.comment)
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
