<template>
  <Modal title="Filtro Relacional" medium @close="$emit('close')">
    <template #right-header>
      <Button @click="clear" with-icon>
        <Icon name="x" size="14" /> Limpar filtros
      </Button>
    </template>
    <p>Mostrar respostas de quem respondeu:</p>
    <Spacing top="1" class="c-relational-filter__container">
      <template v-for="line in lines" :key="line">
        <RelationalFilterLine
          :show-conditional="line.key > 1"
          :questions="questionsKeys"
          :memory="lastFilters[line.key]"
          @remove-filter-line="removeLine(line.key)"
          @choose-answers="addFilter(line.key, $event)"
        />
      </template>
    </Spacing>
    <template #footer>
      <div class="v--flex" style="width: 100%">
        <Button
          @click="addLine"
          class="v--bg-lyzeli-blue"
          with-icon
          v-tooltip="`Adicione uma nova condição`"
        >
          <Icon name="plus" size="14" />
          Adicionar condição
        </Button>
        <div class="v--flex-gap v--flex-align-right">
          <Button @click="$emit('close')">Cancelar</Button>
          <Button class="v--bg-amaranth" @click="play">
            <Icon name="play" size="14" />Executar
          </Button>
        </div>
      </div>
    </template>
  </Modal>
</template>
<script>
import RelationalFilterLine from '@/components/pesquisa/RelationalFilter/FilterLine/FilterLine.vue'
export default {
  name: 'RelationalFilter',
  components: {
    RelationalFilterLine
  },
  props: {
    questions: {
      type: Array,
      required: true
    },
    lastFilters: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['choose-relational-filter', 'close'],
  data() {
    return {
      question: '--',
      answer: '--',
      lines: [{ key: 1 }],
      filters: {}
    }
  },
  beforeMount() {
    if (Object.keys(this.lastFilters).length) {
      this.lines = Object.keys(this.lastFilters).map((key) => ({ key }))
    }
  },
  computed: {
    questionsKeys() {
      return [
        { value: '--', name: '--' },
        ...[...this.questions].map((question) => {
          console.log(question)
          return {
            value: question.column,
            name: question.column + 1,
            classification: question.classification,
            answers: question.answers
          }
        })
      ]
    }
  },
  methods: {
    removeLine(key) {
      this.lines = this.lines.filter((line) => line.key !== key)
      delete this.filters[key]
    },
    addLine() {
      const lastKey = this.lines[this.lines.length - 1]?.key || 0
      this.lines = [...this.lines, { key: lastKey + 1 }]
    },
    addFilter(key, item) {
      this.filters[key] = item
    },
    clear() {
      this.$emit('choose-relational-filter', {})
    },
    play() {
      Object.keys(this.filters).forEach((key) => {
        if (!Object.keys(this.filters[key].chooses).length) {
          delete this.filters[key]
        }
      })

      this.$emit('choose-relational-filter', this.filters)
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
