export default {
  methods: {
    surveys() {
      const { $github, $route, $store } = this

      return {
        get() {
          return $github
            .getFile(
              `${$route.params.org}/${$route.params.repo}`,
              `database/${$route.params.file}/survey.json`
            )
            .then(({ data }) => {
              const survey = JSON.parse(
                Buffer.from(data.content, 'base64').toString('utf8')
              )
              $store.commit('surveys/setSurvey', survey)

              return Promise.resolve({ survey, sha: data.sha })
            })
        },
        test() {
          console.log(this)
        },
        update(updates, message) {
          return this.get().then(({ survey, sha }) => {
            survey = {
              ...this.survey,
              ...survey,
              ...updates
            }
            return this.commit(survey, message, sha)
          })
        },
        commit(survey, message, sha, model = 'survey') {
          survey.updated_at = new Date()

          return $github
            .commit(
              {
                message: message || `Update ${survey.name}`,
                content: JSON.stringify(survey),
                sha
              },
              `${$route.params.org}/${$route.params.repo}`,
              `database/${$route.params.file}/${model}.json`
            )
            .then(() => {
              let s = this[model]
              s = {
                ...survey
              }
              if (model === 'pilot') {
                $store.commit('pilots/setPilot', s)
              } else {
                $store.commit('surveys/setSurvey', s)
              }
              return Promise.resolve(survey)
            })
        }
      }
    },
    comments() {
      const { surveys } = this
      return {
        get() {},
        onQuestion(comment, question) {
          comment = {
            ...comment,
            created_at: new Date()
          }

          return surveys()
            .get()
            .then(({ survey }) => {
              survey.comments = {
                ...(survey.comments || {}),
                questions: {
                  ...(survey?.comments?.questions || {}),
                  [question]: [
                    ...new Set([
                      ...(survey?.comments?.questions?.[question] || []),
                      comment
                    ])
                  ]
                }
              }
              return surveys().update(
                { comments: survey.comments },
                `Add comment on question ${question}`
              )
            })
        },
        deleteFromQuestion(comment, question) {
          return surveys()
            .get()
            .then(({ survey }) => {
              let questionComments =
                survey?.comments?.questions?.[question] || []
              questionComments = questionComments.filter((i) => {
                const check =
                  new Date(i.created_at).getTime() ===
                    new Date(comment.created_at).getTime() &&
                  i.author === comment.author
                return !check
              })
              const comments = {
                ...survey.comments,
                questions: {
                  ...survey.comments.questions,
                  [question]: questionComments
                }
              }
              return surveys().update({ comments }, `Remove comment`)
            })
        }
      }
    },
    tags() {
      const { surveys } = this
      return {
        get() {},
        onQuestion(comment, question) {
          comment = {
            ...comment,
            created_at: new Date()
          }

          return surveys()
            .get()
            .then(({ survey }) => {
              survey.comments = {
                ...(survey.comments || {}),
                questions: {
                  ...(survey?.comments?.questions || {}),
                  [question]: [
                    ...new Set([
                      ...(survey?.comments?.questions?.[question] || []),
                      comment
                    ])
                  ]
                }
              }
              return surveys().update(
                { comments: survey.comments },
                `Add comment on question ${question}`
              )
            })
        }
      }
    }
  }
}
