import axios from 'axios'

export class GitHub {
  http
  ctx
  user
  settingsName = '__lyzeli-general-settings'
  settingsPath = ''
  constructor(app) {
    this.ctx = app
    this.user = this.ctx?.config?.globalProperties?.$store.state.auth.user
    this.http = axios.create({
      baseURL: 'https://api.github.com',
      headers: {
        'content-type': 'application/json',
        accept: 'application/vnd.github.v3+json'
      }
    })
    this.setAuthorization()
  }

  setAuthorization() {
    const token = this.ctx.config.globalProperties?.$storage?.getItem(
      'github.access_token'
    )

    if (token) {
      this.http.defaults.headers.common['Authorization'] = `token ${token}`
    }
  }

  login() {
    const popup = window.open(
      `https://github.com/login/oauth/authorize?client_id=${process.env.VUE_APP_GITHUB_CLIENT_ID}&scope=${process.env.VUE_APP_GITHUB_SCOPE}`,
      'github-oauth-authorize',
      'popup'
    )

    const codeCall = new Promise((resolve, reject) => {
      const interval = window.setInterval(() => {
        if (!popup || popup.closed !== false) {
          window.clearInterval(interval)

          reject(new Error('The popup was closed'))

          return
        }

        if (popup.location.pathname === 'blank') {
          return
        }
        const query = popup.location.search.replace(/^\?/, '')
        const q = query
          .replace(/^\??\//, '')
          .split('&')
          .reduce((values, param) => {
            const [key, value] = param.split('=')
            values = { ...values, [key]: value }
            return values
          }, {})
        resolve(q)
        window.clearInterval(interval)
        popup.close()
      }, 500)
    })
    return codeCall.then((data) => {
      return axios
        .post(`${process.env.VUE_APP_HEROKU}`, { code: data.code })
        .then((response) => {
          this.ctx.config.globalProperties?.$storage?.setItem(
            'github.access_token',
            response.data.access_token
          )
          return this.getProfile().then(({ data }) => {
            this.ctx.config.globalProperties?.$storage.setItem('user', data)

            return this.ctx.config.globalProperties?.$store.dispatch(
              'auth/createUser',
              data
            )
          })
        })
    })
  }

  getProfile() {
    this.setAuthorization()
    return this.http.get(`/user`)
  }

  repository(repo) {
    return repo
      .replace('https://github.com/', '')
      .replace('https://www.github.com', '')
      .replace('.git', '')
  }

  getFile(repo, file) {
    this.setAuthorization()
    return this.http.get(
      `/repos/${this.repository(
        repo
      )}/contents/${file}?timestamp=${new Date().getTime()}`
    )
  }

  createSettings() {
    this.setAuthorization()
    const user = this.ctx.config.globalProperties?.$store.state.auth.user
    const repo = `${user?.login}/${this.settingsName}`

    return this.http
      .get(`/repos/${repo}`)
      .then(() => this.getSettingsFile())
      .catch((error) => {
        if (error.response.status === 404) {
          return this.http
            .post('/user/repos', {
              name: this.settingsName,
              private: true,
              description: 'This is a repository for store Lyzeli settings'
            })
            .then(() => this.getSettingsFile())
        }
      })
  }

  addRepo({ repository, name, survey }) {
    this.setAuthorization()
    const user = this.ctx.config.globalProperties?.$store.state.auth.user
    const repo = `${user?.login}/${this.settingsName}`

    return this.getSettingsFile().then(({ data }) => {
      const str = Buffer.from(data.content, 'base64').toString('utf8')
      const settings = {
        sha: data?.sha,
        content: JSON.parse(str)
      }

      settings.content.push({
        name,
        repository,
        language: survey.language,
        questions: survey.questions.length,
        answers: survey.answers.length
      })

      return this.commit(
        {
          message: `Add ${name}`,
          content: JSON.stringify(settings.content),
          sha: settings.sha
        },
        `${repo}`,
        'database/surveys.json'
      ).then(({ data }) => {
        return Promise.resolve({
          sha: data.content.sha,
          content: settings.content
        })
      })
    })
  }

  removeRepo(repository, name) {
    this.setAuthorization()
    const user = this.ctx.config.globalProperties?.$store.state.auth.user
    const repo = `${user?.login}/${this.settingsName}`

    return this.getSettingsFile().then(({ data }) => {
      const str = Buffer.from(data.content, 'base64').toString('utf8')
      const settings = {
        sha: data?.sha,
        content: JSON.parse(str).filter((i) => {
          return !(i.repository === repository && i.name === name)
        })
      }

      return this.commit(
        {
          message: `Remove ${repository}`,
          content: JSON.stringify(settings.content),
          sha: settings.sha
        },
        `${repo}`,
        'database/surveys.json'
      ).then(({ data }) => {
        return Promise.resolve({
          sha: data.content.sha,
          content: settings.content
        })
      })
    })
  }

  getSettingsFile() {
    const user = this.ctx.config.globalProperties?.$store.state.auth.user
    const repo = `${user.login}/${this.settingsName}`
    return this.getFile(repo, `database/surveys.json`).catch(() => {
      return this.commit(
        {
          message: 'Create survey list',
          content: '[]'
        },
        repo,
        'database/surveys.json'
      )
    })
  }

  getSettings() {
    return this.getSettingsFile().then(({ data }) => {
      return Promise.resolve(
        JSON.parse(Buffer.from(data.content, 'base64').toString('utf8'))
      )
    })
  }

  deleteFile(repo, file, sha) {
    this.setAuthorization()
    return this.http.delete(
      `/repos/${this.repository(
        repo
      )}/contents/${file}?sha=${sha}&message=Delete ${file}`
    )
  }

  deleteFolder(repo, folder) {
    return this.getFile(repo, folder).then((res) => {
      res.data.forEach((i) => {
        this.deleteFile(repo, i.path, i.sha)
      })
    })
  }

  commit(commit, repo, file) {
    this.setAuthorization()
    commit.content = Buffer.from(commit.content).toString('base64')
    commit.committer =
      this.user?.name && this.user?.email
        ? {
            name: this.user?.name,
            email: this.user?.email
          }
        : {
            name: 'Lyzeli',
            email: 'example@email.com'
          }
    commit.author =
      this.user?.name && this.user?.email
        ? {
            name: this.user?.name,
            email: this.user?.email
          }
        : {
            name: 'Lyzeli',
            email: 'example@email.com'
          }
    return this.http.put(
      `/repos/${this.repository(repo)}/contents/${file}`,
      commit
    )
  }
}

export default {
  install: (app) => {
    const github = new GitHub(app)
    app.config.globalProperties.$github = github
  }
}
