<template>
  <div class="c-latex-chart">
    <Spacing class="c-latex-chart__code" vertical="1">
      <b>\usepackage{pgfplots}</b>
    </Spacing>
    <Spacing class="c-latex-chart__code" vertical="1">
      <pre>
        {{ chart }}
      </pre>
    </Spacing>
    <div class="v--flex">
      <Button class="v--flex-align-right v--bg-light-blue" @click="copy">
        Copiar
      </Button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'LatexChart',
  props: {
    set: {
      type: Object,
      default: () => ({})
    },
    total: {
      type: Number,
      default: 0
    }
  },
  computed: {
    chart() {
      return `
\\begin{figure}[th]
    \\begin{tikzpicture}
        \\begin{axis}[
            xbar, xmin=0,
            tick label style = {
                font = {\\fontsize{8 pt}{10 pt}\\selectfont}
            },
            bar width = 10,
            y=0.5cm,
            symbolic y coords={
                ${Object.keys(this.set)
                  .sort((a, b) => {
                    return this.set[b] - this.set[a]
                  })
                  .join(',\n                ')}
            },
            ytick=data,
            grid=major, grid style={dashed,gray!30},
            xticklabel={$\\pgfmathprintnumber{\\tick}\\%$},
            legend={q}
        ]
           \\addplot[fill=black!30!blue] coordinates {
              ${Object.keys(this.set)
                .sort((a, b) => {
                  return this.set[b] - this.set[a]
                })
                .map((answer) => {
                  const num = this.set[answer]
                  return `(${
                    this.total > 0
                      ? `${((num * 100) / this.total).toFixed(2)},${answer})`
                      : `0,${answer}`
                  }\n`
                })
                .join('              ')}            };
        \\end{axis}
    \\end{tikzpicture}
    \\captionof{figure}{Put your caption}
    \\label{put-your-label}
\\end{figure}`
    }
  },
  methods: {
    copy() {
      this.$copyText(this.chart)
        .then(() => {
          this.$toast.show('Copiado!')
        })
        .catch(() => {
          this.$toast.error('Erro ao copiar!')
        })
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
