import Files from '@/helpers/Files'
import Str from '@/helpers/Str'

export class QuestionClassifier {
  options
  language = ''

  constructor() {
    const raw = require.context(`../contents/questions`, true, /.*\.(js)$/)
    this.options = Files.getComponents(raw).map(
      (component) => component.default
    )
  }

  classify(questions, language) {
    this.language = language
    return questions.map((question) => this.predict(question).key)
  }

  getOption(key) {
    return this.options.find((item) => item.key === key)
  }

  getType(key) {
    const option = this.getOption(key)
    return option && option.type
  }

  predict(question) {
    const options = this.options.sort((a, b) => a.weight - b.weight)

    for (let i = 0; i < options.length; i++) {
      const option = options[i]
      const positions = ['startsWith', 'endsWith', 'includes'].filter(
        (i) => option[i]
      )

      for (let j = 0; j < positions.length; j++) {
        if (
          Str.finder(
            positions[j],
            question,
            option[positions[j]][this.language],
            true
          )
        ) {
          return option
        }
      }
    }

    return options[options.length - 1]
  }
}

export default {
  install: (app) => {
    const questionClassifier = new QuestionClassifier()
    app.config.globalProperties.$questionClassifier = questionClassifier
  }
}
