<template>
  <div class="c-field" :class="styles">
    <label v-if="label" :for="fieldId" class="c-field__label">
      {{ label }}
    </label>
    <p v-if="sublabel" class="c-field__sublabel">
      {{ sublabel }}
    </p>
    <fieldset class="c-field__fieldset">
      <textarea
        :id="fieldId"
        class="c-field__input"
        :class="{
          'c-field__input--large': large
        }"
        :autofocus="autofocus"
        :disabled="disabled"
        :required="required"
        :placeholder="placeholder"
        :autocapitalize="autocapitalize"
        :autocorrect="autocorrect"
        :rows="rows"
        :value="modelValue || (!changed && value) || ''"
        @input="input"
      />
    </fieldset>
  </div>
</template>
<script>
export default {
  name: 'FieldText',
  emits: ['update:modelValue'],
  props: {
    modelValue: String,
    value: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: ''
    },
    rows: {
      type: [Number, String],
      default: 2
    },
    autocapitalize: {
      type: String,
      default: ''
    },
    autocorrect: {
      type: String,
      default: ''
    },
    label: {
      type: String,
      default: ''
    },
    sublabel: {
      type: String,
      default: ''
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    silent: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    required: {
      type: Boolean,
      default: false
    },
    large: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      passwordIsVisible: false,
      changed: false
    }
  },
  computed: {
    fieldId() {
      return `field-${this.name}`
    },
    styles() {
      return {
        [this.getClass('silent')]: this.silent
      }
    }
  },
  mounted() {
    if (!this.modelValue && this.value) {
      this.input({ target: { value: this.value } })
    }
  },
  methods: {
    input(e) {
      this.changed = true
      this.$emit('update:modelValue', e.target.value)
    },
    changePasswordVisibility() {
      return (this.passwordIsVisible = !this.passwordIsVisible)
    },
    getClass(name) {
      return `c-field--${name}`
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
