import CryptoJS from 'crypto-js'
export default {
  methods: {
    getUserCoding(user) {
      return this.$github
        .getFile(
          `${this.$route.params.org}/${this.$route.params.repo}`,
          `database/${this.$route.params.file}/coding/${user.id}.json`
        )
        .then(({ data }) => {
          const b64 = Buffer.from(data.content, 'base64').toString('utf8')
          let codes = {}
          if (b64.length > 1) {
            const bytes = CryptoJS.AES.decrypt(b64, `${user.id}`).toString(
              CryptoJS.enc.Utf8
            )

            codes = JSON.parse(bytes)
          }

          this.$store.commit('codes/setCodes', codes)
          console.log('data', codes)
          return Promise.resolve({ codes: codes || {}, sha: data.sha })
        })
        .catch((error) => {
          if (error?.response?.status === 404) {
            this.$toast.error('A pesquisa solicitada não existe')
          } else {
            this.$toast.error('Houve um erro. Por favor, tente novamente.')
          }
        })
    },
    updateUserCoding(user, updates, message) {
      return this.getUserCoding(user).then(({ codes, sha }) => {
        codes = {
          ...codes,
          ...updates
        }
        return this.commitUserCodingUpdates(user, codes, message, sha)
      })
    },
    commitUserCodingUpdates(user, codes, message, sha) {
      codes = {
        ...this.codes,
        ...codes,
        updated_at: new Date()
      }

      return this.$github
        .commit(
          {
            message: message || `Update codes`,
            content: CryptoJS.AES.encrypt(
              JSON.stringify(codes),
              `${user.id}`
            ).toString(),
            sha
          },
          `${this.$route.params.org}/${this.$route.params.repo}`,
          `database/${this.$route.params.file}/coding/${user.id}.json`
        )
        .then(() => {
          this.$store.commit('codes/setCodes', codes)
          return Promise.resolve(codes)
        })
    },
    createTagOnQuestion(user, tag) {
      tag = {
        ...tag,
        created_at: new Date()
      }

      return this.getUserCoding(user).then(({ codes, sha }) => {
        const questions = {
          ...(codes?.tags?.list?.questions || {}),
          [this.$route.params.question - 1]: [
            ...new Set([
              ...(codes?.tags?.questions?.[this.$route.params.question - 1] ||
                []),
              tag
            ])
          ]
        }
        const exists =
          codes?.tags?.list?.find((item) => item.name === tag.name) || false
        let list = [...(codes?.tags?.list || [])]
        if (!exists) {
          list = [...new Set([...(codes?.tags?.list || []), tag])]
        }

        codes.tags = {
          ...(codes.tags || {}),
          list,
          questions
        }

        return this.commitUserCodingUpdates(
          user,
          codes,
          `Add tag on question ${this.$route.params.question}`,
          sha
        )
      })
    },
    createTag(user, tag) {
      tag = {
        ...tag,
        created_at: new Date()
      }

      return this.getUserCoding(user).then(({ codes, sha }) => {
        codes.tags = {
          ...(codes.tags || {}),
          list: [...new Set([...(codes?.tags?.list || []), tag])]
        }

        return this.commitUserCodingUpdates(
          user,
          codes,
          `Add tag on question ${this.$route.params.question}`,
          sha
        )
      })
    },
    createCodingComment(user, comment, question, answer) {
      comment = {
        ...comment,
        encrypt: true,
        created_at: new Date()
      }

      return this.getUserCoding(user).then(({ codes, sha }) => {
        codes = {
          ...this.codes,
          ...codes
        }
        codes.comments = {
          ...(codes?.comments || {}),
          answers: {
            ...(codes?.comments?.answers || {}),
            [question]: {
              ...(codes?.comments?.answers?.[question] || {}),
              [answer]: [
                ...new Set([
                  ...(codes?.comments?.answers?.[question]?.[answer] || []),
                  comment
                ])
              ]
            }
          }
        }
        return this.commitUserCodingUpdates(
          user,
          codes,
          `Add comment on question ${question}, answer ${answer}`,
          sha
        )
      })
    },
    getSurvey() {
      return this.$github
        .getFile(
          `${this.$route.params.org}/${this.$route.params.repo}`,
          `database/${this.$route.params.file}/survey.json`
        )
        .then(({ data }) => {
          const survey = JSON.parse(
            Buffer.from(data.content, 'base64').toString('utf8')
          )
          this.$store.commit('surveys/setSurvey', survey)

          return Promise.resolve({ survey, sha: data.sha })
        })
        .catch((error) => {
          if (error?.response?.status === 404) {
            this.$toast.error('A pesquisa solicitada não existe')
          } else {
            this.$toast.error('Houve um erro. Por favor, tente novamente.')
          }
        })
    },
    getPilot() {
      return this.$github
        .getFile(
          `${this.$route.params.org}/${this.$route.params.repo}`,
          `database/${this.$route.params.file}/pilot.json`
        )
        .then(({ data }) => {
          const pilot = JSON.parse(
            Buffer.from(data.content, 'base64').toString('utf8')
          )
          this.$store.commit('pilots/setPilot', pilot)

          return Promise.resolve({ pilot, sha: data.sha })
        })
        .catch((error) => {
          if (error?.response?.status === 404) {
            this.$toast.error('A pesquisa piloto solicitada não existe')
          } else {
            this.$toast.error('Houve um erro. Por favor, tente novamente.')
          }
        })
    },
    updateSurvey(updates, message) {
      return this.getSurvey().then(({ survey, sha }) => {
        survey = {
          ...this.survey,
          ...survey,
          ...updates
        }
        return this.commitUpdates(survey, message, sha)
      })
    },
    updatePilot(updates, message) {
      return this.getPilot().then(({ pilot, sha }) => {
        pilot = {
          ...this.pilot,
          ...pilot,
          ...updates
        }
        return this.commitUpdates(pilot, message, sha, 'pilot')
      })
    },
    commitUpdates(survey, message, sha, model = 'survey') {
      survey.updated_at = new Date()

      return this.$github
        .commit(
          {
            message: message || `Update ${survey.name}`,
            content: JSON.stringify(survey),
            sha
          },
          `${this.$route.params.org}/${this.$route.params.repo}`,
          `database/${this.$route.params.file}/${model}.json`
        )
        .then(() => {
          let s = this[model]
          s = {
            ...survey
          }
          if (model === 'pilot') {
            this.$store.commit('pilots/setPilot', s)
          } else {
            this.$store.commit('surveys/setSurvey', s)
          }
          return Promise.resolve(survey)
        })
    },
    commentOnQuestion(comment, question) {
      comment = {
        ...comment,
        created_at: new Date()
      }

      return this.getSurvey().then(({ survey, sha }) => {
        survey.comments = {
          ...(survey.comments || {}),
          questions: {
            ...(survey?.comments?.questions || {}),
            [question]: [
              ...new Set([
                ...(survey?.comments?.questions?.[question] || []),
                comment
              ])
            ]
          }
        }
        return this.commitUpdates(
          survey,
          `Add comment on question ${question}`,
          sha
        )
      })
    },
    commentOnAnswer(comment, question, answer) {
      comment = {
        ...comment,
        created_at: new Date()
      }

      const comments = {
        ...this.survey.comments,
        answers: {
          ...(this.survey?.comments?.answers || {}),
          [question]: {
            ...(this.survey?.comments?.answers?.[question] || {}),
            [answer]: [
              ...new Set([
                ...(this.survey?.comments?.answers?.[question]?.[answer] || []),
                comment
              ])
            ]
          }
        }
      }
      console.log({ comments })
      return this.updateSurvey(
        { comments },
        `Add comment on question ${question}, answer ${answer}`
      )
    },
    doComment(comment, userId) {
      return this.$github
        .getFile(
          `${this.$route.params.org}/${this.$route.params.repo}`,
          `database/${this.$route.params.file}/${userId}.json`
        )
        .then(({ data }) => {
          let base = JSON.parse(
            Buffer.from(data.content, 'base64').toString('utf8')
          )

          base = {
            ...base,
            comments: {
              ...base.comments,
              comment
            }
          }

          base.updated_at = new Date()

          return this.$github
            .commit(
              {
                message: `Add comment`,
                content: JSON.stringify(base),
                sha: data.sha
              },
              `${this.$route.params.org}/${this.$route.params.repo}`,
              `database/${this.$route.params.file}/${userId}.json`
            )
            .then(() => {
              return this.$store.commit('surveys/setSurvey', base)
            })
        })
    }
    // getComments() {
    //   return this.$github.getFile
    // }
  }
}
