<template>
  <Dropdown
    :circle="!large"
    hide-indicator
    :change-name-on-select="false"
    :button-classes="['v--bg-white', 'v--text-color-bright-blue']"
    class="c-tag-creator"
  >
    <Icon v-if="loading" class="c-tag-creator__icon" name="loader" size="14" />
    <Icon v-else name="tag-icon" size="14" />
    <Spacing v-if="large" left="1"> Alterar tags </Spacing>
    <template v-slot:content>
      <div class="c-tag-creator__content">
        <div class="v--flex-gap v--flex-align-middle">
          <div style="width: 100%">
            <Field
              name="tag"
              small
              placeholder="Identificação da tag"
              autocomplete="off"
              autofocus
              v-model="name"
            />
          </div>
          <span
            v-if="!(list && list.length)"
            class="c-tag-creator__colors-item c-tag-creator__colors-item--round"
            :style="{
              'background-color': `#${color}`,
              'flex-shrink': '0',
              border: '1px solid'
            }"
          />
        </div>
        <template v-if="!(list && list.length)">
          <ul class="c-tag-creator__colors">
            <li
              class="c-tag-creator__colors-item"
              v-for="hash in colors"
              :key="hash"
              :style="{
                'background-color': `#${hash}`
              }"
              @click="color = hash"
            />
          </ul>
          <small style="padding: 0 4px">Escolha uma cor</small>
          <Spacing top="1">
            <Button
              :loading="loading"
              block
              softly
              class="v--bg-amaranth"
              @click="createAndAddTag"
            >
              Adicionar Tag
            </Button>
          </Spacing>
        </template>

        <ul v-else class="c-tag-creator__tags">
          <li
            v-for="tag in list"
            class="c-tag-creator__tags-item"
            :key="tag"
            :style="{
              'background-color': `#${tag.color}`,
              color: getTextColor(tag.color)
            }"
            @click="used(tag) ? justDetachTag(tag) : justAddTag(tag)"
          >
            <PulseLine v-if="loading" />
            <template v-else>
              <span class="c-tag-creator__tags-hover" />
              <div class="v--flex v--flex-align-middle">
                <Spacing :right="1 / 2">
                  <Icon :name="used(tag) ? 'check' : 'circle'" size="13" />
                </Spacing>
                {{ tag.name }}
              </div>
            </template>
          </li>
        </ul>
      </div>
    </template>
  </Dropdown>
</template>
<script>
import textColor from '@/mixins/text-color'
