<template>
  <div>
    <Line
      :styles="{ 'max-width': '500px', margin: '0 auto' }"
      :chart-data="data"
      :chart-options="options"
    />
    <Button with-icon @click="$emit('download', 'line-chart')">
      <Icon name="download" size="14" />Baixar gráfico
    </Button>
  </div>
</template>
<script>
import { Line } from 'vue-chartjs'
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale
} from 'chart.js'

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale
)

export default {
  name: 'LineChart',
  components: { Line },
  props: {
    question: {
      type: Object,
      required: true
    }
  },
  data() {
    const { items, length, title } = this.question

    return {
      //   done: null,
      //   legend: [
      //     { background: '#6bd06b', name: 'Aprovações' },
      //     { background: '#ff5d5d', name: 'Reprovações por nota' },
      //     { background: '#6e8392', name: 'Trancamentos' },
      //     { background: '#fde050', name: 'Reprovações por falta' },
      //     { background: 'rgba(153, 102, 255, 0.2)', name: 'Alunos cursando' }
      //   ],
      data: items
        ? {
            labels: Object.keys(items),
            datasets: [
              {
                label: `Nº de respostas: ${(items && length) || 0}`,
                maxBarThickness: 40,
                data: Object.values(items).map((i) =>
                  ((i * 100) / (length || 1)).toFixed(3)
                ),
                backgroundColor: [
                  '#6bd06b',
                  '#ff5d5d',
                  '#6e8392',
                  '#fde050',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)'
                ]
              }
            ]
          }
        : null,
      options: {
        plugins: {
          title: {
            display: true,
            text: title,
            fontColor: '#2c3e50'
          },
          tooltip: {
            callbacks: {
              title(data) {
                return data[0].label
              },
              label(item) {
                return ` ${item.formattedValue}%`
              }
            }
          }
        }
      }
    }
  }
}
</script>
