<template>
  <div v-if="resource === 'chart'">
    <Spacing vertical="1" v-if="showToolbar">
      <Toolbar
        :valids="valids.length"
        :invalids="invalids.length"
        :answers="answers.length"
        :use-sentiments="false"
        @change-status="setKind($event)"
      />
    </Spacing>
    <HorizontalChart
      :set="set"
      :total="itemsLength"
      :disable-interactions="!showToolbar"
      @show-answers="showAnswers"
    />
  </div>
</template>
<script>
import Toolbar from '@/components/pesquisa/answers/Toolbar/Toolbar.vue'
export default {
  name: 'Groups',
  emits: ['show-answers'],
  components: {
    Toolbar
  },
  props: {
    answers: {
      type: Array,
      default: () => []
    },
    open: {
      type: Boolean,
      default: true
    },
    showToolbar: {
      type: Boolean,
      default: true
    },
    printStyle: {
      type: String,
      default: 'Groups'
    }
  },
  data() {
    return {
      kind: 'valids',
      lastResource: 'chart',
      resource: 'chart'
    }
  },
  created() {
    if (!this.open) {
      this.setResource('')
    }
  },
  watch: {
    open(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.setResource(newValue ? this.lastResource : '')
      }
    }
  },
  computed: {
    valids() {
      return this.answers.filter((i) => this.$answerClassifier.isValid(i.text))
    },
    invalids() {
      return this.answers.filter((i) => !this.$answerClassifier.isValid(i.text))
    },
    itemsLength() {
      return this[this.kind].length
    },
    multipleGroupsItems() {
      return this[this.kind].reduce((set, item) => {
        const keys = item.text === '' ? '' : item.text.split(',')
        keys.forEach((key) => {
          const k = key.trim()
          set = {
            ...set,
            [k]: set[k] ? set[k] + 1 : 1
          }
        })

        return set
      }, {})
    },
    set() {
      if (this.printStyle === 'MultipleGroups') {
        return this.multipleGroupsItems
      }

      return this[this.kind].reduce((set, item) => {
        const key = item.text === '' ? '' : item.text
        set = {
          ...set,
          [key]: set[key] ? set[key] + 1 : 1
        }

        return set
      }, {})
    },
    latexItems() {
      return { total: this.answers.length, set: this.set }
    }
  },
  methods: {
    setKind(value) {
      this.kind = value
    },
    setResource(value) {
      this.lastResource = this.resource || 'chart'
      this.resource = this.resource === value ? '' : value
    },
    showAnswers(label) {
      return this.$emit(
        'show-answers',
        this.answers
          .filter((item) => {
            if (this.printStyle === 'MultipleGroups') {
              const ansrs = item.text.split(',').map((k) => k.trim())
              return ansrs.some((j) => j === label)
            }
            return item.text === label
          })
          .map((item) => item.line)
      )
    }
  }
}
</script>
