export default {
  namespaced: true,
  state: () => ({ answers: {}, noanswers: {}, filtereds: [] }),
  mutations: {
    setAnswers(state, answers) {
      state.answers = answers
    },
    setNoAnswers(state, noanswers) {
      state.noanswers = noanswers
    },
    setFiltereds(state, filtereds) {
      state.filtereds = filtereds
    }
  }
}
