export class Phonetics {
  pt(str) {
    const output = this.removeAccent(str) // Remove a acentuação
      .toUpperCase() // Coloca todas as letras na forma maiúscula
      .trim() // Remove espaços em branco do início e do final da palavra
      .replace(/lh/gi, '1') // Transforma dígrafo lh no átomo 1
      .replace(/nh/gi, '2') // Transforma dígrafo nh no átomo 2
      .replace(/rr/gi, '3') // Transforma dígrafo rr no átomo 3
      .replace(/^r/i, '3') // Palavras que iniciam com R tem o mesmo som do dígrafo rr, então são transformadas no átomo 3

      .replace(/([a-z]*)([a-r]|[t-z])s(a|e|i|o|u)/gi, '$1$2Z$3') // Palavras que tem S seguido de vogal após a primeira letra da palavra tem o S transformado em Z

      .replace(/ss/gi, 'S') // Transforma o dígrafo SS no átomo S
      .replace(/ç/gi, 'S') // Transforma o Ç no átomo S

      .replace(/([a-z]*)(s|x)(c|ç)(e|i)/gi, '$1S$4') // Transforma SC, SÇ, XC e XÇ no átomo S

      .replace(/(c|s)h/gi, 'X') // Transforma CH e SH no átomo X

      .replace(/([a-z]*)([aeiou])l((?![aeiou])[a-z]*)/gi, '$1$2U$3') // Transforma L precedido de vogal e seguido por consoante no átomo U

      .replace(/([a-z]*)c(e|i)/gi, '$1S$2') // Transforma C seguido de E ou I no átomo S
      .replace(/([a-z]*)c(a|o|u)/gi, '$1K$2') // Transforma C seguido de A, O ou U no átomo K

      .replace(/([a-z]*)g(e|i)/gi, '$1J$2') // Transforma G seguido de E ou I no átomo J
      .replace(/([a-z]*)gu(e|i)/gi, '$1G$2') // Transforma GU seguido de E ou I no átomo G

      .replace(/([a-z]*)qu(e|i)/gi, '$1K$2') // Transforma QU seguido de E ou I no átomo K

      .replace(/Q/gi, 'K') // Transforma o Q no átomo K
      .replace(/ph/gi, 'F') // Tranforma o PHP no átomo F

      .replace(/h/gi, '') // Remove as ocorrências de H (para lidar com H mudo)
      .replace(/[\s,(\-.]/g, '/')
      .replace(/[/]+/g, '/')
      .replace(/\)/g, '')
      .trim('/')
    return output
  }

  removeAccent(str) {
    if (str) {
      const map = {
        a: 'á|à|ã|â|À|Á|Ã|Â',
        e: 'é|è|ê|É|È|Ê',
        i: 'í|ì|î|Í|Ì|Î',
        o: 'ó|ò|ô|õ|Ó|Ò|Ô|Õ',
        u: 'ú|ù|û|ü|Ú|Ù|Û|Ü',
        n: 'ñ|Ñ'
      }

      str = str.toLowerCase()

      for (const pattern in map) {
        str = str.replace(new RegExp(map[pattern], 'g'), pattern)
      }
    }

    return str
  }

  latenize(str) {
    if (str) {
      const map = {
        a: 'á|à|ã|â|À|Á|Ã|Â',
        e: 'é|è|ê|É|È|Ê',
        i: 'í|ì|î|Í|Ì|Î',
        o: 'ó|ò|ô|õ|Ó|Ò|Ô|Õ',
        u: 'ú|ù|û|ü|Ú|Ù|Û|Ü',
        c: 'ç|Ç',
        n: 'ñ|Ñ'
      }

      str = str.toLowerCase()

      for (const pattern in map) {
        str = str.replace(new RegExp(map[pattern], 'g'), pattern)
      }
    }

    return str
  }
}

export default {
  install: (app) => {
    const phonetics = new Phonetics(app)
    app.config.globalProperties.$phonetics = phonetics
  }
}
