<template>
  <div class="c-submissions">
    <SubmissionLoading v-if="$wait.is('survey')" />
    <div v-else class="v--flex" style="width: 100%">
      <Dropdown
        v-if="!showingAnswers"
        tooltip="Altere o tipo das respostas"
        title="Escolha um tipo para ver"
        placement="bottom-end"
        :initial="selectedAnswersKind"
        :items="answersKind"
        @change="changeAnswersKind"
      >
        Exibindo
      </Dropdown>
      <div class="v--flex-gap v--flex-align-right" v-if="this[kind].length">
        <Button
          v-if="kind === 'answers'"
          @click="$emit('archive', currentAnswerLine)"
          with-icon
          v-tooltip="
            `Ao arquivar, toda a folha de resposta deixará de ser contabilizada`
          "
          class="v--bg-amaranth"
        >
          <Icon name="archive" />
          <span> Arquivar </span>
        </Button>
        <Button
          v-else
          class="v--bg-positive"
          with-icon
          v-tooltip="
            `Ao desarquivar, toda a folha de resposta será contabilizada`
          "
          @click="$emit('unarchive', currentAnswerLine)"
        >
          <Icon name="corner-left-up" />
          <span> Desarquivar </span>
        </Button>
      </div>
    </div>
    <template v-if="this[kind].length">
      <Spacing class="v--flex" vertical="3">
        <span class="c-submissions__title">
          Respondente nº {{ currentAnswerLine + 1 }}
        </span>
        <Paginator
          class="v--flex-align-right"
          :paginator="paginator"
          @load-page="goToAnswer"
        />
      </Spacing>

      <QuestionsContainer>
        <template v-for="(question, index) in questions" :key="index">
          <Question
            :ref="`question-${index}`"
            :question="question"
            :short="true"
          >
            <p
              class="v--code-box"
              :class="{
                'v--text-color-transparent': $wait.is('loading-submission')
              }"
              v-html="
                question?.[kind].find((a) => a.line === this.currentAnswerLine)
                  ?.text || ''
              "
            />
          </Question>
        </template>
      </QuestionsContainer>
    </template>
    <EmptyListMessage v-else />
  </div>
</template>
<script>
import { mapState } from 'vuex'
import SubmissionLoading from '@/components/pesquisa/Submissions/Loading/Loading.vue'
import Question from '@/components/pesquisa/Question/Question.vue'
import QuestionsContainer from '@/components/pesquisa/QuestionsContainer/QuestionsContainer.vue'

export default {
  name: 'SurveySubmissions',
  components: {
    QuestionsContainer,
    SubmissionLoading,
    Question
  },
  props: {
    questions: {
      type: Array,
      required: true
    },
    showingAnswers: {
      type: Array,
      default: () => {
        return undefined
      }
    }
  },
  emits: ['archive', 'unarchive'],
  data() {
    return {
      currentAnswerIndex: 0,
      selectedAnswersKind: { name: 'Respostas Ativas', value: 'answers' }
    }
  },
  computed: {
    ...mapState({
      answers: (state) => state.answers.answers,
      noanswers: (state) => state.answers.noanswers,
      filtereds: (state) => state.answers.filtereds
    }),
    kind() {
      return this.selectedAnswersKind.value
    },
    currentAnswerLine() {
      if (Array.isArray(this.showingAnswers)) {
        return this.showingAnswers[this.currentAnswerIndex] || 0
      }
      return (
        this.questions[0]?.[this.selectedAnswersKind.value]?.[
          this.currentAnswerIndex
        ]?.line || 0
      )
    },
    answersKind() {
      return [
        { name: 'Respostas Ativas', value: 'answers' },
        { name: 'Respostas Arquivadas', value: 'noanswers' }
      ]
    },
    paginator() {
      return {
        current: this.currentAnswerIndex + 1,
        total: Array.isArray(this.showingAnswers)
          ? this.showingAnswers.length
          : this[this.kind].length
      }
    }
  },
  methods: {
    changeAnswersKind($event) {
      this.currentAnswerIndex = 0
      this.selectedAnswersKind = $event
    },
    goToAnswer(page) {
      this.$wait.start('loading-submission')
      setTimeout(() => {
        this.currentAnswerIndex = page - 1
        this.$wait.end('loading-submission')
      }, 300)
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
