<template>
  <div class="c-word-cloud">
    <div class="c-word-cloud__first-child">
      <template v-for="(word, index) in wordsToPrint" :key="index">
        <div
          class="c-word-cloud__word"
          @click="$emit('click', word)"
          :style="{ 'font-size': fontSize(word.weight) + 'px', ...styles }"
        >
          {{ word.text }}
        </div>
      </template>
    </div>
  </div>
</template>
<script>
export default {
  name: 'WordCloud',
  props: {
    words: {
      type: Array,
      default: () => []
    },
    animationOverlap: {
      type: Number,
      default: 1
    },
    enterAnimation: {
      type: [Object, String],
      default: () => ({ opacity: 0 })
    },
    leaveAnimation: {
      type: [Object, String],
      default: () => ({ opacity: 0 })
    },
    presentation: {
      type: String,
      default: 'line'
    }
  },
  data() {
    return {
      elementWidth: 0,
      elementHeight: 0,
      progress: null
    }
  },
  computed: {
    firstWord() {
      return this.wordsPolished[0] || null
    },
    lastWord() {
      const words = this.wordsPolished
      return words[words.length - 1] || null
    },
    wordsToPrint() {
      return this[this.presentation]
    },
    wordsPolished() {
      return Object.keys(this.words)
        .map((text) => {
          const weight = this.words[text]

          return {
            text,
            weight
          }
        })
        .sort((a, b) => b.weight - a.weight)
    },
    line() {
      return [...this.wordsPolished]
    },
    styles() {
      return {
        display: 'inline-block'
      }
    }
  },
  methods: {
    fontSize(weight) {
      return (weight / this.firstWord.weight) * 100
    }
  }
}
</script>
<style lang="stylus" scoped>
.c-word-cloud
//     height 100%
//     position relative
  width 100%

