import stopwords from '@/contents/phrases/stopwords'

export class Word {
  count(phrase, language) {
    const raw = Word.normalizeAndRemoveAllNumbers(phrase)
      .toLowerCase()
      .replace("'", '')
      .replace('/', ' ')
      .split(' ')
      .filter(
        (k) =>
          !stopwords[language].map((s) => Word.normalize(s)).includes(k) &&
          k.length > 1
      )
      .sort((a, b) => (a > b ? 1 : b > a ? -1 : 0))

    const data = raw.reduce((acc, item) => {
      acc[item] = acc[item] ? acc[item] + 1 : 1
      return acc
    }, {})

    console.log(data)
    return Object.keys(data).map((key) => [key, data[key]])
  }

  static normalize(text) {
    return text
      .toLowerCase()
      .normalize('NFD')
      .replace(/[\u0300-\u036f]/gi, '')
      .replace(/,/g, '')
      .replace(/[“”]/g, '')
      .replace(/\(/g, '')
      .replace(/\)/g, '')
      .replace(/^-/g, '')
      .replace(/\./g, '')
  }

  static normalizeAndRemoveNumbers(text, regex = /^\d+/g) {
    return this.normalize(text)
      .replace(regex, '')
      .replace(/^\./g, '')
      .replace(/^-/g, '')
      .replace(/^_/g, '')
      .trim()
  }

  static normalizeAndRemoveAllNumbers(text) {
    return Word.normalizeAndRemoveNumbers(text, /\d+/g)
  }

  static finder(type, text, options, removeNumbers) {
    if (!(options && options.length)) {
      return false
    }
    const normalizer = removeNumbers ? 'normalizeAndRemoveNumbers' : 'normalize'

    const found = options.find((item) =>
      this[normalizer](text).trim()[type](this[normalizer](item))
    )

    return found
  }

  static ucfirst(str) {
    return str.charAt(0).toUpperCase() + str.slice(1)
  }
}

export default {
  install: (app) => {
    const word = new Word()
    app.config.globalProperties.$word = word
  }
}
