<template>
  <div
    class="about"
    v-if="(cards && cards.length) || $wait.is('getting-settings')"
  >
    <PesquisaToolbar
      @filter-cards="filterCards"
      @change-template="template = $event"
    />
    <Spacing top="4">
      <PesquisasLoading v-if="$wait.is('getting-settings')" />
      <PesquisaCardsContainer v-else :horizontal="template === 'list'">
        <PesquisaCard
          v-for="(pesquisa, index) in cards"
          :key="index"
          :horizontal="template === 'list'"
          :pesquisa="pesquisa"
          @delete-survey="showDeleteSurvey = $event"
        />
      </PesquisaCardsContainer>
    </Spacing>

    <Modal
      v-if="showDeleteSurvey"
      :title="`Excluir a pesquisa ${showDeleteSurvey.name}?`"
      small
      @close="showDeleteSurvey = false"
    >
      <template v-slot:footer>
        <div class="v--flex-gap v--flex-align-right">
          <Button
            @click="showDeleteSurvey = false"
            :disabled="$wait.is('removing-repo')"
          >
            Cancelar
          </Button>
          <Button
            class="v--bg-amaranth"
            @click="deleteSurvey"
            :loading="$wait.is('removing-repo')"
          >
            Excluir pesquisa
          </Button>
        </div>
      </template>
    </Modal>
  </div>
  <div v-else class="p-home">
    <div class="p-home__welcome">
      <h1 class="p-home__title">Você ainda não tem pesquisas</h1>
      <Tag class="v--bg-lyzeli-blue" />
      <Tag class="v--bg-amaranth" />
      <Tag class="v--bg-lyzeli-yellow" />
      <Spacing vertical="3" class="p-home__text">
        <p>
          Lyzeli é um sistema pensado para gerenciar e ajudar com a análise de
          pesquisas de Survey.
        </p>
        <p>
          Crie uma pesquisa agora, e entre para um mundo onde você pode se
          dedicar às grandes ideias, e deixar o trabalho com a gente :)
        </p>
        <Spacing vertical="2">
          <Button
            to="/pesquisas/novo?origem=/inicio"
            fat
            class="v--bg-amaranth"
          >
            Criar uma pesquisa agora
          </Button>
        </Spacing>
      </Spacing>
    </div>
  </div>
</template>
<script>
import { defineComponent } from 'vue'
import { mapState } from 'vuex'
import PesquisaCard from '@/components/pesquisas/Card/Card.vue'
import PesquisaCardsContainer from '@/components/pesquisas/CardsContainer/CardsContainer.vue'
import PesquisaToolbar from '@/components/pesquisas/Toolbar/Toolbar.vue'
import PesquisasLoading from '@/components/pesquisas/LoadingList/LoadingList.vue'
export default defineComponent({
  name: 'About',
  route: '/inicio',
  layout: 'painel',
  components: {
    PesquisaCard,
    PesquisaCardsContainer,
    PesquisaToolbar,
    PesquisasLoading
  },
  data() {
    return {
      filterLanguage: null,
      template: 'grid',
      showDeleteSurvey: false
    }
  },
  computed: {
    ...mapState({
      settings: (state) => state.auth.settings
    }),
    cards() {
      return this.settings?.content?.filter((card) => {
        if (this.filterLanguage) {
          return card.language === this.filterLanguage
        }
        return card
      })
    }
  },
  created() {
    this.template = localStorage.getItem('disciplines-template') || 'grid'
  },
  methods: {
    check(itens) {
      itens.forEach((i) => console.log(this.$phonetics.pt(i)))
    },
    similarity(word, options) {
      const p1 = this.$phonetics.pt(word)
      return options.map((i) => {
        const p = this.$phonetics.pt(i)
        const regex = new RegExp(`^${p1}`)
        const o = p.replace(regex, '')

        return {
          target: word,
          selection: i,
          phonems: [p1, p],
          similarity: ((p.length - o.length) * 100) / (p.length || 1)
        }
      })
    },
    filterCards($event) {
      this.filterLanguage = $event.value
    },
    deleteSurvey() {
      this.$wait.start('removing-repo')
      const repo = this.$github.repository(this.showDeleteSurvey.repository)
      const githubLink = `https://github.com/${repo}`
      this.$github
        .deleteFolder(githubLink, `database/${this.showDeleteSurvey.name}`)
        .then(() => {
          return this.$github
            .removeRepo(githubLink, this.showDeleteSurvey.name)
            .then((settings) => {
              this.$toast.success('Removido!')
              return this.$store
                .dispatch('auth/createSettings', settings)
                .then(() => {
                  this.whatDoing = 'Concluído!'
                  this.$router.push('/inicio')
                })
            })
        })
        .finally(() => {
          this.showDeleteSurvey = null
          this.$wait.end('removing-repo')
        })
    }
  }
})
</script>
