  display flex
  flex-direction column
  align-items center
  justify-content center
  height 100%
  &__title
    font-weight 500
    font-size 20px
  &__welcome
    max-width 600px
    margin 0 auto
  &__text
    font-size 16px
    line-height 1.3
</style>
