<template>
  <div
    class="c-progress-bar"
    :class="{
      'c-progress-bar--bar-only': barOnly,
      'c-progress-bar--disable-interactions': disableInteractions
    }"
  >
    <span v-if="!barOnly">{{ progress.width.toFixed(2) }}%</span>
    <div class="c-progress-bar__bar">
      <span class="c-progress-bar__label">{{ label }}</span>
      <div class="c-progress-bar__progress-false" :style="progressStyle" />
      <div class="c-progress-bar__progress" :style="progressStyle" />
    </div>
    <Button v-if="!barOnly" fit @click="$emit('show-answers')">
      {{ value }} {{ value === 1 ? 'resposta' : 'respostas' }}
    </Button>
  </div>
</template>
<script>
export default {
  name: 'ProgressBar',
  props: {
    label: {
      type: String,
      default: ''
    },
    value: {
      type: null,
      default: undefined
    },
    total: {
      type: [Number, String],
      default: 0
    },
    percentage: {
      type: null,
      default: undefined
    },
    barOnly: {
      type: Boolean,
      default: false
    },
    disableInteractions: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    progress() {
      let width = 0
      if (this.percentage === undefined) {
        if (this.value !== undefined && this.total !== undefined) {
          width = this.calcPercentage(this.value, this.total)
        } else if (this.label.match(/[0-9]+(,)+[0-9]*( %)/)) {
          width = this.label.replace(',', '.').replace(' %', '')
        }
      } else {
        width = this.percentage
      }

      return {
        width
      }
    },
    progressStyle() {
      return { width: `${this.progress.width}%` }
    }
  },
  methods: {
    calcPercentage(value, max) {
      if (max == 0) return 0
      let result = (value * 100) / max

      return result >= 100 ? 100 : result
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped></style>
