<template>
  <div>
    <HorizontalChart
      v-if="resource === 'chart'"
      :set="set"
      :total="answers.length"
      @show-answers="showAnswers"
    />
  </div>
</template>
<script>
export default {
  name: 'Terms',
  emits: ['show-answers'],
  props: {
    answers: {
      type: Array,
      default: () => []
    },
    open: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      lastResource: 'chart',
      resource: 'chart'
    }
  },
  created() {
    if (!this.open) {
      this.setResource('')
    }
  },
  watch: {
    open(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.setResource(newValue ? this.lastResource : '')
      }
    }
  },
  computed: {
    set() {
      return this.answers.reduce((set, item) => {
        set = {
          ...set,
          [item.text]: set[item.text] ? set[item.text] + 1 : 1
        }
        return set
      }, {})
    },
    latexItems() {
      return { total: this.answers.length, set: this.set }
    }
  },
  methods: {
    showAnswers(label) {
      return this.$emit(
        'show-answers',
        this.answers
          .filter((item) => item.text === label)
          .map((item) => item.line)
      )
    },
    setResource(value) {
      this.lastResource = this.resource || 'chart'
      this.resource = this.resource === value ? '' : value
    }
  }
}
</script>
