export default {
  data() {
    return {
      showRemoveModal: false
    }
  },
  methods: {
    deleteQuestionComment(comment) {
      this.$wait.start('removing-comment')
      let questionComments =
        this.survey?.comments?.questions?.[this.question.column] || []
      questionComments = questionComments.filter((i) => {
        const check =
          new Date(i.created_at).getTime() ===
            new Date(comment.created_at).getTime() &&
          i.author === comment.author
        return !check
      })

      const comments = {
        ...this.survey.comments,
        questions: {
          ...this.survey.comments.questions,
          [this.question.column]: questionComments
        }
      }

      this.updateSurvey({ comments }, `Remove comment`)
        .then(() => this.$toast.success(`Comentário removido`))
        .finally(() => {
          this.showRemoveModal = false
          this.$wait.end('removing-comment')
        })
    },
    deleteAnswerComment(comment) {
      this.$wait.start('removing-comment')
      console.log(comment)
      let answerComments =
        this.survey?.comments?.answers?.[comment.question]?.[comment.answer] ||
        []
      answerComments = answerComments.filter((i) => {
        const check =
          new Date(i.created_at).getTime() ===
            new Date(comment.created_at).getTime() &&
          i.author === comment.author
        return !check
      })

      const comments = {
        ...this.survey.comments,
        answers: {
          ...this.survey.comments.answers,
          [comment.question]: {
            ...(this.survey?.comments?.answers?.[comment.question] || {}),
            [comment.answer]: answerComments
          }
        }
      }

      this.updateSurvey({ comments }, `Remove comment`)
        .then(() => this.$toast.success(`Comentário removido`))
        .finally(() => {
          this.showRemoveModal = false
          this.$wait.end('removing-comment')
        })
    }
  }
}
