import { createStore } from 'vuex'

const modulesFiles = require.context('./modules', true, /.*\.(js)$/)
const modules = {
  ...modulesFiles.keys().reduce((modules, module) => {
    const name = module.replace('./', '').replace('.js', '')
    modules[name] = modulesFiles(module).default
    return modules
  }, {})
}

// Create a new store instance.
const store = createStore({
  modules: modules
})

export default store
