<template>
  <div class="c-question" :class="{ 'c-question--loading': loading }">
    <div class="c-question__loader">
      <Icon name="loader" class="c-question__loader-icon" size="30" />
    </div>
    <div class="c-question__container">
      <div class="v--flex">
        <div>
          <p class="c-question__id">
            Questão {{ question.column + 1 }} -
            {{ question.answers?.length }} respostas
          </p>
          <p class="c-question__title">
            {{ question.title }}
          </p>
          <p class="c-question__subtitle">
            <Icon :name="classification?.icon" size="10" />
            {{ classification?.title?.[survey.language] }}
          </p>
          <template v-if="!short">
            <template
              v-if="
                !encrypt &&
                tagsObject?.questions?.[question.column] &&
                (!short || hideAnswer)
              "
            >
              <Spacing top="1" class="v--flex-gap">
                <Tag
                  v-for="tag in tagsObject?.questions[question.column]"
                  :key="tag"
                  :style="{
                    'background-color': `#${tag.color}`,
                    color: getTextColor(tag.color)
                  }"
                >
                  {{ tag.name }}
                </Tag>
              </Spacing>
            </template>
            <template v-else>
              <div class="v--flex-gap">
                <Tag
                  v-for="tag in codes?.tags?.questions[question.column]"
                  :key="tag"
                  :style="{
                    'background-color': `#${tag.color}`,
                    color: getTextColor(tag.color)
                  }"
                >
                  {{ tag.name }}
                </Tag>
              </div>
            </template>
          </template>
        </div>
        <div v-if="!short" class="v--flex-gap v--flex-align-right">
          <Button
            v-if="maximize"
            circle
            @click="maximize = false"
            v-tooltip="'Minimizar'"
          >
            <Icon name="minimize" />
          </Button>
          <Button
            v-if="!maximize"
            circle
            @click="maximize = true"
            v-tooltip="'Maximizar'"
          >
            <Icon name="maximize" size="16" />
          </Button>
          <Button
            circle
            @click="showLatexModal"
            v-tooltip="'Exportar para LaTeX'"
          >
            <Icon name="export-icon" size="16" />
          </Button>
          <Dropdown
            tooltip="Altere a classificação da pergunta"
            title="Escolha uma classificação para a pergunta"
            hide-indicator
            circle
            placement="bottom-end"
            :changeNameOnSelect="false"
            :items="settings"
            @change="
              $emit('change-class', {
                question: this.question.column,
                class: $event.value
              })
            "
          >
            <Icon name="settings" size="16" />
          </Dropdown>
        </div>
      </div>
      <Spacing :top="1.5">
        <slot v-if="$slots.default" />
        <component
          v-else-if="!hideAnswer && !loading"
          ref="answers"
          :is="answerStyle"
          :answers="question.answers"
          :question="question.column"
          :open="maximize"
          :encrypt="encrypt"
          :print-style="classification.answers.printStyle"
          @show-answers="$emit('show-answers', $event)"
        />
        <Spacing
          v-if="hideAnswer || !short"
          top="1"
          class="c-question__countable"
        >
          <div class="c-question__countable-icon">
            <Button
              circle
              class="v--bg-white v--text-color-bright-blue"
              @click="$emit('do-comment')"
              v-tooltip="'Comentar'"
            >
              <Icon name="message-circle" size="14" />
            </Button>
            <span>{{ commentsList.length || '' }}</span>
          </div>
          <div class="c-question__countable-icon">
            <TagCreator
              v-tooltip="'Alterar tags'"
              :tags="tagsObject?.list || []"
              :loading="$wait.is('changing-tag')"
              :useds="tagsObject?.questions?.[question.column]"
              @create-and-add-tag="createAndAddTag"
              @add-tag="createAndAddTag"
              @detach-tag="removeTag"
            />
            <span>
              {{ tagsObject?.questions?.[question.column]?.length || '' }}
            </span>
          </div>
          <Button
            v-if="kind === 'survey'"
            circle
            class="v--bg-white v--text-color-bright-blue"
            @click="showChartsModal"
            v-tooltip="'Exibir gráficos'"
          >
            <Icon name="chart" size="14" />
          </Button>
          <Button
            v-if="kind === 'survey'"
            circle
            class="v--bg-white v--text-color-bright-blue"
            @click="showLatexModal"
            v-tooltip="'Exportar para LaTeX'"
          >
            <Icon name="export-icon" size="14" />
          </Button>
        </Spacing>
      </Spacing>
      <CommentsContainer v-if="!short">
        <Comment
          v-for="(comment, index) in commentsList"
          :comment="comment"
          :key="index"
          @delete="$emit('remove-comment', { comment, question })"
        />
      </CommentsContainer>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Terms from '@/components/pesquisa/answers/Terms/Terms.vue'
import Groups from '@/components/pesquisa/answers/Groups/Groups.vue'
import YesOrNot from '@/components/pesquisa/answers/YesOrNot/YesOrNot.vue'
import OpenQuestion from '@/components/pesquisa/answers/OpenQuestion/OpenQuestion.vue'
import Comment from '@/components/pesquisa/Comment/Comment.vue'
import CommentsContainer from '@/components/pesquisa/CommentsContainer/CommentsContainer.vue'
import github from '@/mixins/survey'
import textColor from '@/mixins/text-color'
import survey from '@/mixins/survey'

export default {
  name: 'Question',
  components: {
    Terms,
    Groups,
    YesOrNot,
    Comment,
    CommentsContainer,
    OpenQuestion
  },
  emits: [
    'do-comment',
    'show-answers',
    'show-latex',
    'loaded',
    'change-class',
    'remove-comment',
    'show-charts'
  ],
  mixins: [github, survey, textColor],
  props: {
    question: {
      type: Object,
      default: () => ({})
    },
    open: {
      type: Boolean,
      default: true
    },
    encrypt: {
      type: Boolean,
      default: false
    },
    short: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    hideAnswer: {
      type: Boolean,
      default: false
    },
    kind: {
      type: String,
      default: 'survey'
    },
    reloadAnswer: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      maximize: this.open,
      showRemoveModal: null
    }
  },
  computed: {
    ...mapState({
      codes: (state) => state.codes.codes,
      survey: (state) => state.surveys.survey,
      pilot: (state) => state.pilots.pilot,
      user: (state) => state.auth.user
    }),
    tagsObject() {
      return this.encrypt ? this.codes?.tags : this[this.kind]?.tags
    },
    commentsList() {
      return this[this.kind]?.comments?.questions?.[this.question.column] || []
    },
    classification() {
      return this.$questionClassifier.getOption(this.question.classification)
    },
    answerStyle() {
      if (
        ['Groups', 'MultipleGroups', 'TimeStamp'].includes(
          this.classification.answers.printStyle
        )
      ) {
        return 'Groups'
      }
      return this.classification.answers.printStyle
    },
    settings() {
      return [
        {
          name: 'Múltipla escolha de resposta única',
          value: 'groupsInAnswer',
          icon: 'check-circle'
        },
        {
          name: 'Múltipla escolha de resposta múltipla',
          value: 'multipleGroupsInAnswer',
          icon: 'check-square'
        },
        {
          name: 'Questão aberta',
          value: 'openQuestion',
          icon: 'align-justify'
        },
        { name: 'Termos e Condições', value: 'terms', icon: 'alert-triangle' },
        { name: 'Marcação de tempo', value: 'timestamp', icon: 'clock' },

        { name: 'Escala linear', value: 'npsLike', icon: 'more-horizontal' },
        { name: 'sim ou não', value: 'yesOrNot', icon: 'power' }
      ]
    }
  },
  watch: {
    open(novo) {
      this.maximize = novo
    }
  },
  mounted() {
    this.$emit('loaded')
  },
  methods: {
    removeTag(tag) {
      this.$wait.start('changing-tag')
      const tags = {
        ...(this[this.kind].tags || {}),
        list: [...new Set([...(this[this.kind]?.tags?.list || []), tag])],
        questions: {
          ...(this[this.kind]?.tags?.questions || {}),
          [this.question.column]: [
            ...new Set([
              ...(this[this.kind]?.tags?.questions?.[
                this.question.column
              ]?.filter((i) => i.name !== tag.name) || [])
            ])
          ]
        }
      }

      this.surveys()
        .update({ tags }, `Remove tag`)
        .then(() => this.$toast.success(`Tag "${tag.name}" removida`))
        .finally(() => this.$wait.end('changing-tag'))
    },
    createAndAddTag(tag) {
      this.$wait.start('changing-tag')
      if (
        this[this.kind]?.tags?.questions?.[this.question.column]?.find(
          (item) => item.name === tag.name
        )
      ) {
        this.$toast.error(`Tag "${tag.name}" já existe`)
        this.$wait.end('changing-tag')
        return
      }
      const tags = {
        ...(this[this.kind].tags || {}),
        list: [...new Set([...(this[this.kind]?.tags?.list || []), tag])],
        questions: {
          ...(this[this.kind]?.tags?.questions || {}),
          [this.question.column]: [
            ...new Set([
              ...(this[this.kind]?.tags?.questions?.[this.question.column] ||
                []),
              tag
            ])
          ]
        }
      }
      if (this.encrypt) {
        this.createTagOnQuestion(this.user, tag)
          .then(() =>
            this.$toast.success(`Tag "${tag.name}" de coding adicionada`)
          )
          .finally(() => this.$wait.end('changing-tag'))
      } else if (this.kind === 'pilot') {
        this.updatePilot({ tags }, `Add tag`)
          .then(() => this.$toast.success(`Tag "${tag.name}" adicionada`))
          .finally(() => this.$wait.end('changing-tag'))
      } else {
        this.surveys()
          .update({ tags }, `Add tag`)
          .then(() => this.$toast.success(`Tag "${tag.name}" adicionada`))
          .finally(() => this.$wait.end('changing-tag'))
      }
    },
    showLatexModal() {
      this.$emit('show-latex', this.$refs.answers.latexItems)
    },
    showChartsModal() {
      this.$emit('show-charts', {
        title: this.question.title,
        items: this.$refs.answers.set,
        length: this.$refs.answers.itemsLength
      })
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
