<template>
  <div class="c-navbar">
    <router-link to="/inicio">
      <Icon class="c-navbar__logo" name="lyzeli-cube" :size="30" />
    </router-link>
    <nav class="c-navbar__nav">
      <ul class="c-navbar__ul">
        <li
          v-for="item in nav"
          :key="item.icon"
          class="c-navbar__li c-navbar__link"
        >
          <router-link v-if="item.to" :to="item.to" :exact="false">
            {{ item.name }}
          </router-link>
          <span v-else>{{ item.name }}</span>
        </li>
        <li class="c-navbar__li">
          <Button class="v--bg-amaranth" @click="goToNewDataset">
            Criar nova pesquisa
          </Button>
        </li>
        <li class="c-navbar__li c-navbar__link">
          <div>
            <Dropdown
              placement="bottom-end"
              :items="userOptions"
              :change-name-on-select="false"
              @change="chooseUserOption"
            >
              <img
                class="c-navbar__avatar"
                :src="user.avatar_url"
                :alt="user.name || 'avatar'"
              />
            </Dropdown>
          </div>
        </li>
      </ul>
      <Button class="c-navbar__menu-toggle" @click="showMenuModal">
        <Icon name="menu-toggle" />
      </Button>
    </nav>
    <Modal v-if="menuModal" @close="closeMenuModal">
      <template v-slot:header>
        <div class="c-navbar__modal-nav">
          <router-link to="/inicio" @click="closeMenuModal">
            <Icon class="c-navbar__logo" name="meforma" :size="30" />
          </router-link>
          <Button class="v--bg-amaranth" @click="goToNewDataset">
            Criar nova pesquisa
          </Button>
        </div>
      </template>
      <ul class="c-navbar__modal-ul">
        <li
          v-for="item in nav"
          :key="item.icon"
          class="c-navbar__modal-li"
          @click="closeMenuModal"
        >
          <router-link
            class="c-navbar__modal-li-content"
            v-if="item.to"
            :to="item.to"
          >
            <div class="v--flex v--flex-align-middle">
              <Icon :name="item.icon" size="13" />
              <Spacing left="1">
                {{ item.name }}
              </Spacing>
            </div>
            <Icon name="chevron-right" size="14" />
          </router-link>
          <span v-else>{{ item.name }}</span>
        </li>
      </ul>
      <Spacing vertical="2">
        <Line />
      </Spacing>
      <ul class="c-navbar__modal-ul">
        <li
          v-for="item in userOptions"
          :key="item.name"
          class="c-navbar__modal-li"
          @click="chooseUserOption(item)"
        >
          <router-link
            v-if="item.to"
            :to="item.to"
            class="c-navbar__modal-li-content"
          >
            <div class="v--flex v--flex-align-middle">
              <Icon :name="item.icon" size="13" />
              <Spacing left="1">
                {{ item.name }}
              </Spacing>
            </div>
            <Icon name="chevron-right" size="14" />
          </router-link>
          <span v-else class="v--flex v--flex-align-middle">
            <span class="c-navbar__modal-li-content">
              <div class="v--flex v--flex-align-middle">
                <Icon :name="item.icon" size="13" />
                <Spacing left="1">
                  {{ item.name }}
                </Spacing>
              </div>
            </span>
          </span>
        </li>
      </ul>
    </Modal>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'Navbar',
  data() {
    return {
      siacModal: false,
      menuModal: false
    }
  },
  computed: {
    ...mapState({
      user: (state) => state.auth.user
    }),
    nav() {
      return [{ name: 'Início', to: '/inicio', icon: 'book-open' }]
    },
    userOptions() {
      return [
        // { name: 'Editar perfil', to: '/perfil/configuracoes', icon: 'edit' },
        { name: 'Ajuda', to: '/ajuda', icon: 'help-circle' },
        { name: 'Sair', value: 'logout', icon: 'log-out' }
      ]
    }
  },
  methods: {
    chooseUserOption(item) {
      if (item.value) {
        this[item.value](item)
      }
      this.closeMenuModal()
    },
    logout() {
      this.$store.dispatch('auth/logout').then(() => {
        this.$storage.clear()
        this.$router.push('/')
      })
    },
    goToNewDataset() {
      this.$router.push({
        path: '/pesquisas/novo',
        query: {
          origem: this.$route.path
        }
      })
    },
    closeSiacModal() {
      this.siacModal = false
    },
    showMenuModal() {
      this.menuModal = true
    },
    closeMenuModal() {
      this.menuModal = false
    }
  }
}
</script>

<style src="./style.styl" lang="stylus" scoped />
