<template>
  <div class="c-selection-bar">
    <div class="c-selection-bar__board">
      <Button
        class="c-selection-bar__button"
        @click="$emit('relational-filter')"
      >
        {{ what }}
      </Button>
      <div class="c-selection-bar__board-right v--flex">
        <Spacing left="1">
          <Button
            class="c-selection-bar__button"
            @click="clear"
            v-tooltip="`Limpar filtro relacional`"
          >
            <Icon name="x" :size="14" />
          </Button>
        </Spacing>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'SelectionBar',
  emits: ['click', 'clear', 'relational-filter'],
  computed: {
    what() {
      return 'Você está vendo resultados com um Filtro Relacional'
    }
  },
  methods: {
    clear() {
      this.$emit('clear', this.items)
    }
  }
}
</script>
