<template>
  <div class="c-filter-line">
    <!-- <div>
      <Tag
        :class="{
          'v--bg-lyzeli-blue': conditional === 'AND',
          'v--bg-lyzeli-yellow': conditional === 'OR'
        }"
      />
      <FieldSelect
        v-if="showConditional"
        name="condition"
        :selected="conditional"
        :options="[
          { name: 'AND', value: 'AND' },
          { name: 'OR', value: 'OR' }
        ]"
        style="width: 100px"
        @change="changeConditional"
      />
    </div> -->
    <div>
      <Tag v-if="showConditional" class="v--bg-lyzeli-yellow">E</Tag>
    </div>
    <div class="c-filter-line__container">
      <div class="v--flex v--flex-align-middle">
        Na questão
        <div>
          <FieldSelect
            name="question"
            :options="questions"
            :selected="question"
            style="width: 100px"
            v-model="question"
          />
        </div>
        , a resposta
      </div>
      <div class="c-filter-line__answer-options">
        <template v-for="(line, index) in lines" :key="line.key">
          <div class="v--flex v--flex-align-middle">
            <span v-if="index > 0"> ou</span>
            <Spacing v-else horizontal="1" />
            <FieldSelect
              name="answer"
              :options="answers"
              :selected="line.value || ''"
              style="width: 300px"
              @change="addChoose(line.key, $event)"
            />
            <Button
              v-if="index > 0"
              class="v--bg-light-meforma-pink"
              v-tooltip="`Remover opção de resposta`"
              @click="removeLine(line.key)"
            >
              <Icon name="x" size="14" />
            </Button>
            <div class="v--flex-gap" v-else>
              <Button
                class="v--bg-lyzeli-blue"
                with-icon
                @click="addLine"
                v-tooltip="`Adicionar outra opção de resposta para a questão`"
              >
                <Icon name="plus" size="14" /> OU
              </Button>
              <Button
                class="v--bg-light-meforma-pink"
                v-tooltip="`Excluir condição inteira`"
                @click="$emit('remove-filter-line')"
              >
                <Icon name="trash" size="14" />
              </Button>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'RelationalFilterLine',
  props: {
    questions: {
      type: Array,
      required: true
    },
    showConditional: {
      type: Boolean,
      default: true
    },
    memory: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['choose-answers', 'remove-filter-line'],
  data() {
    return {
      question: '--',
      lines: [{ key: 1 }],
      conditional: 'AND',
      chooses: {}
    }
  },
  beforeMount() {
    if (Object.keys(this.memory).length) {
      this.question = `${this.memory.question}`

      if (Object.keys(this.memory.chooses).length) {
        this.lines = Object.keys(this.memory.chooses).map((key) => ({
          key: parseInt(key),
          value: this.memory.chooses[key]
        }))
        this.chooses = { ...this.memory.chooses }
      }
      this.choose()
    }
  },
  computed: {
    answers() {
      const q = this.questions.find((i) => i.value === parseInt(this.question))
      let set = []
      if (q?.answers) {
        q.answers.forEach((i) => {
          if (q.classification === 'multipleGroupsInAnswer') {
            const ansrs = i.text.split(',').map((k) => k.trim())
            ansrs.forEach((a) => {
              if (!set.find((j) => j.value === a) && a !== '') {
                set.push({ value: a, name: a })
              }
            })
          } else {
            if (!set.find((j) => j.value === i.text) && i.text !== '') {
              set.push({ value: i.text, name: i.text })
            }
          }
        })
      }
      set = set.sort((a, b) => {
        if (a.value > b.value) {
          return 1
        }
        if (a.value < b.value) {
          return -1
        }

        return 0
      })

      return [{ value: '--', name: '--' }, ...set]
    }
  },
  methods: {
    removeLine(key) {
      this.lines = this.lines.filter((line) => line.key !== key)
      this.removeChoose(key)
    },
    addLine() {
      const lastKey = this.lines[this.lines.length - 1]?.key || 0
      this.lines = [...this.lines, { key: lastKey + 1 }]
    },
    addChoose(key, choose) {
      if (choose !== '--') {
        this.chooses[key] = choose
        this.choose()
      } else {
        this.removeChoose(key)
      }
    },
    removeChoose(key) {
      delete this.chooses[key]
      this.choose()
    },
    changeConditional(conditional) {
      this.conditional = conditional
      this.choose()
    },
    choose() {
      const q = this.questions.find((i) => i.value === parseInt(this.question))

      const lines = q.answers
        .filter((a) => {
          if (q.classification === 'multipleGroupsInAnswer') {
            const ansrs = a.text.split(',').map((k) => k.trim())
            return ansrs.some((j) => Object.values(this.chooses).includes(j))
          }
          return Object.values(this.chooses).includes(a.text)
        })
        .map((i) => i.line)

      this.$emit('choose-answers', {
        // conditional: this.conditional,
        question: parseInt(this.question),
        chooses: this.chooses,
        lines
      })
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
