export class CsvReader {
  rows = []
  titles = []
  ctx

  constructor(app) {
    this.ctx = app
  }

  read(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()

      if (file && file.size) {
        reader.readAsText(file)

        reader.onload = () => {
          const raw = typeof reader.result === 'string' ? reader.result : ''
          resolve(raw.split(/\r?\n|\r/).map((row) => row.split(/\t/)))
        }
      } else {
        reject()
      }
    })
  }
}

export default {
  install: (app) => {
    const csvReader = new CsvReader(app)
    app.config.globalProperties.$csvReader = csvReader
  }
}
