<template>
  <Modal title="Excluir o comentário?" small @close="$emit('close')">
    <Comment :comment="comment" hide-delete-button />
    <template v-slot:footer>
      <div class="v--flex-gap v--flex-align-right">
        <Button
          @click="$emit('close')"
          :disabled="$wait.is('removing-comment')"
        >
          Cancelar
        </Button>
        <Button
          class="v--bg-amaranth"
          @click="$emit('delete')"
          :loading="$wait.is('removing-comment')"
        >
          Excluir comentário
        </Button>
      </div>
    </template>
  </Modal>
</template>
<script>
import Comment from '@/components/pesquisa/Comment/Comment.vue'
export default {
  name: 'CommentDeleteModal',
  components: {
    Comment
  },
  props: {
    comment: {
      type: Object,
      required: true
    }
  },
  emits: ['close', 'delete']
}
</script>
