import { createApp } from 'vue'
import { Toaster } from '@meforma/vue-toaster'
import wait from '@meforma/vue-wait-for'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/css/main.styl'
// import tooltip from './directives/tooltip'
import global from './components/global'
import github from './plugins/github'
import storage from './plugins/storage'
import word from './plugins/word'
import phonetics from './plugins/phonetics'
import wc from './plugins/word-cloud'
import csvReader from './plugins/csv-reader'
import questionClassifier from './plugins/question-classifier'
import answerClassifier from './plugins/answer-classifier'
import copyText from '@meforma/vue-copy-to-clipboard'
import VueWordCloud from '@/components/VueWordCloud/src'
import VTooltip from 'v-tooltip'

const options = {
  defaultPlacement: 'top',
  defaultClass: 'c-tooltip',
  defaultTargetClass: 'has-tooltip',
  defaultHtml: true,
  defaultTemplate:
    '<div class="c-tooltip" role="tooltip"><div class="c-tooltip__arrow"></div><div class="c-tooltip__inner"></div></div>',
  defaultArrowSelector: '.c-tooltip__arrow',
  defaultInnerSelector: '.c-tooltip__inner',
  defaultDelay: {
    show: 400
  },
  disposeTimeout: 500000,
  popover: {
    defaultPlacement: 'bottom',
    defaultClass: 'vue-popover-theme',
    defaultBaseClass: 'c-tooltip c-popover',
    defaultWrapperClass: 'wrapper',
    defaultInnerClass: 'c-tooltip__inner c-popover__inner',
    defaultArrowClass: 'c-tooltip__arrow c-popover__arrow',
    defaultOpenClass: 'open',
    defaultDelay: 0,
    defaultTrigger: 'click',
    defaultOffset: 8,
    defaultContainer: 'body',
    defaultBoundariesElement: undefined,
    defaultPopperOptions: {},
    defaultAutoHide: true,
    defaultHandleResize: true
  }
}

const app = createApp(App)

global.keys().forEach((file) => {
  const component = global(file).default
  if (component.name) {
    app.component(component.name, component)
  } else {
    throw 'Global components requires name attribute'
  }
})

// app.directive('tooltip', {
//   mounted(el, bindings) {
//     if (bindings.value) {
//       el._tooltipValue = bindings.value
//       tooltip(el, { placement: bindings.arg })
//     }
//   },
//   updated(el, bindings) {
//     if (bindings.value) {
//       el._tooltipValue = bindings.value
//     }
//   }
// })

app.config.globalProperties.$github = github
app.provide('$github', github)
app.provide('$storage', storage)
app
  .use(router)
  .use(store)
  .use(wait)
  .use(csvReader)
  .use(copyText)
  .use(wc)
  .component(VueWordCloud.name, VueWordCloud)
  .use(questionClassifier)
  .use(answerClassifier)
  .use(github)
  .use(word)
  .use(phonetics)
  .use(storage)
  .use(Toaster)
  .use(VTooltip, options)
  .mount('#app')
